class modeform extends Component {
  constructor(props) {
    super(props)
    this.state = {
      time: '',
      con: '',
      ttime: ''
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      time: nextProps.time,
      con: nextProps.con,
      ttime: nextProps.ttime
    })
  }
  componentDidMount() {
      console.log(this)
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        span: 4
      },
      wrapperCol: {
        span: 20
      }
    }
    return (
      <div className="news">
        <Form >
          {/* <Form.Item label="跟进时间" {...formItemLayout}>
            {getFieldDecorator('time', {
              rules: [{ required: false, message: '请选择跟进时间!' }],
              initialValue: this.state.time
            })(
              <Input />
              // <DatePicker showTime placeholder="请选择跟进时间" onChange={onChange} onOk={onOk} style={{width:394}} />,
            )}
          </Form.Item> */}
          <Form.Item label="跟进内容" {...formItemLayout}>
            {getFieldDecorator('content', {
              rules: [{ required: false, message: '请输入跟进内容!' }],
              initialValue: this.state.con
            })(
              <TextArea rows={4} />,
            )}
          </Form.Item>
          <Form.Item label="提醒日期" {...formItemLayout}>
            {getFieldDecorator('t_time', {
              rules: [{ required: false, message: '请选择提醒日期!' }],
              initialValue: this.state.ttime
            })(
              <Input />
              //  <DatePicker showTime placeholder="请选择提醒日期" onChange={onChange} onOk={onOk} style={{width:394}}  />
            )}
          </Form.Item>
        </Form>
      </div>
    )
  }
}
const modeforms = Form.create()(modeform)
export default modeforms