import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
  Table,
  Icon,
  LocaleProvider,
  Menu, 
  Dropdown, } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
class staffForm  extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              axios.post('/index.php/integral/sou',{
                name:user,
                num:loginid,
                names:values.projectname
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                  console.log(res.data.data)
                  var num=res.data.data
                  var array=[];
                  if(num.length>0){
                    num.map((v,j)=>{
                      array.push({
                              id:v.id,
                             key:v.bid,
                             projectname:v.name,
                             zjjifen:v.integral,
                             zjtime:v.update_time,
                             zongjifen:v.total_integral,
                             pai:v.pai,
                         })
                         this.setState({
                          datanum:array,
                          loading:false
                         })
                     })
                  }else{
                    this.setState({
                      datanum:'',
                      loading:false
                     })
                  }
                 

                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
            }
          }
        });
      };
      handleSelectChange(){

      }
      tiaoDetail(num){
        this.props.history.push({pathname:'/projectmanage/projectInformation/projectdetail',data:num});
      }
      componentDidMount(){
        //项目列表
       this.getjifenStatus();
     //区域数据
    

      }
      getjifenStatus(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/integral/index',{
            name:user,
            num:loginid
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
                console.log(res.data.data);
               var number=res.data.data;
               var array=[];
               if(number.length>0){
               number.map((v,j)=>{
                array.push({
                        id:v.id,
                       key:v.bid,
                       projectname:v.name,
                       zjjifen:v.integral,
                       zjtime:v.update_time,
                       zongjifen:v.total_integral,
                       pai:v.pai,
                   })
                   this.setState({
                    datanum:array,
                    loading:false
                   })
               })
              }else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      tiaolink(){
        this.props.history.push('/personmanage/addstaff');
      }
      goDetail=(num)=>{
        console.log(num);
        this.props.history.push({pathname:'/personInformation/jifendetail',data:num});
      }

      deleteji(num){
        console.log(num)
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/integral/delete',{
            name:user,
            num:loginid,
            id:num
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log("删除成功")
              this.getjifenStatus();
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
      }

      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '项目名称',
            dataIndex: 'projectname',
          },
          {
            title: '增减积分',
            dataIndex: 'zjjifen',
          },
          {
            title: '最近调整时间',
            dataIndex: 'zjtime',
          },
          {
            title: '总积分',
            dataIndex: 'zongjifen',
          },
          {
            title: '项目排名',
            dataIndex: 'pai',
          },
          {
            title: '操作',
            dataIndex: 'handal',
            render:(text,record)=><div style={{cursor:'pointer'}}><span style={{color:'rgba(24,144,255,1)'}} onClick={this.goDetail.bind(this,record.key)}>详情</span>&nbsp;| &nbsp;<span style={{color:'rgba(24,144,255,1)'}} onClick={this.deleteji.bind(this,record.id)}>删除</span></div>
          },
         
        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item ><Link to="/personInformation/jifenstatus"> 个人信息</Link></Breadcrumb.Item>
                  <Breadcrumb.Item><Link to="/personInformation/jifenstatus">项目积分 </Link></Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
              
                <Form.Item label="项目名称" layout="inline">
                {getFieldDecorator('projectname', {
                    rules: [{ required: false, message: '请输入项目名称' }],
                  })(
                    <Input placeholder="请输入项目名称"/>
                  )}
                  
                  </Form.Item>
           
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     <br/>
                     <br/>
                     <br/>
                     <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p>
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'3px'}} >删除</Button>
                   
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const jifenstatus = Form.create()(staffForm);
export default jifenstatus