import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,

} from 'antd';
import '../../../style/addtuangou.scss'
import axios from 'axios'
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={

        }
        
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            console.log(key);
            axios.post('/index.php/tuan/save',{
              bid:key,
              value:values,
              name:user,
              num:loginid
         }).then((res)=>{
                if(res.data.code==200){
                 console.log('提交团购成功')
               }
         }).catch((error)=>{
               console.log(error)
         })
        }

          }
        });
      };
      tiaoEdit(){
           this.props.history.push('/projectmanage/projectInformation/edittuangou');
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
    <div className="addtuangou">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
            <Breadcrumb.Item href="">添加团购 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">团购标准：</p>
            <Form.Item >
            {getFieldDecorator('title', {
                })(
            <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} />,
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">开始时间：</p>
            <Form.Item >
            {getFieldDecorator('start', {
                })(
            <Input style={{width:'1000px'}}/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">结束时间：</p>
            <Form.Item >
            {getFieldDecorator('end', {
                })(
            <Input  style={{width:'1000px'}}/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary"  onClick={this.tiaoEdit.bind(this)}>
            编辑团购
          </Button>
          <Button type="primary" htmlType="submit" className="sub">
          提交
          </Button>
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou