import React ,{Component} from 'react';
import { Breadcrumb} from 'antd'
import '../../style/personInformation/chengjiaodetail.scss'
import axios from 'axios'
import getallArea from '../common/getArea'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class chengjiaodetail extends Component{
      constructor(props){
        super(props)
        this.state={
              detail:[],
              element:[],
              protocol:[],
              confirm:[],
              site:[],
        }
      }
      componentDidMount(){
          this.getDetail();
      }
      getDetail(){
        var key=this.props.location.data;
        console.log(key);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/recording/edit',{
            name:user,
            num:loginid,
            id:key
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log(res.data.data);
              this.setState({
                    detail:res.data.data,
                    element:res.data.data.element,
                    protocol:res.data.data.protocol,
                    confirm:res.data.data.confirm,
                    site:res.data.data.site,
              })
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      render(){
        const { detail,element, protocol, confirm, site } =this.state;
        return(
          <div className="chengjiaodetail">
              <Breadcrumb>
                  <Breadcrumb.Item href="/"> 个人信息</Breadcrumb.Item>
                  <Breadcrumb.Item href="">成交情况 </Breadcrumb.Item>
                  <Breadcrumb.Item href="">成交详情</Breadcrumb.Item>
                </Breadcrumb>

            <div className="base">
                <h3 className="title">基本信息</h3>
                <br style={{color:'rgba(237,237,237,1)'}}/>
               
                  <ul className="ulbase">
                    <li><span>客户编号：</span>{detail.id}</li>
                    <li><span>成交项目：</span>{detail.project}</li>
                    <li><span>成交日期：</span>{detail.time }</li>
                    <li><span>成交金额：</span>{detail.money}</li>

                    <li><span>业绩：</span>{ detail.yeji}</li>
                     <li><span>客户姓名：</span>{detail.name}</li>
                     <li><span>客户电话：</span>{detail.tel}</li>
                     <li><span>成交房源：</span>{detail.house}</li>

                     <li><span>付款方式：</span>{detail.type}</li>
                     <li><span>预计签约时间：</span>{detail.q_time}</li>
                     <li><span>预计付首付/全款时间：</span>{detail.p_time}</li>
                   
                  </ul>
            </div>
            <br/>
            <div className="family">
                 <dl>
                     <dt><a href="">成交三要素</a></dt>
                     {
                     element.length>0? element.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      }) :''
                     }
                 </dl>
                 <dl>
                     <dt><a href="">返佣协议</a></dt>
                     {
                   protocol.length>0? protocol.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      }) :''
                     }
                 </dl>
                 <dl>
                     <dt><a href="">返佣确认单</a></dt>
                     {
                      confirm.length>0?  confirm.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      }):''
                     }
                 </dl>
                 <dl>
                     <dt><a href="">成交现场确认照片</a></dt>
                     {
                      site.length>0? site.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      }):''
                     }
                 </dl>
            </div>
           
           
          </div>
          )
      }
}
  export default chengjiaodetail