import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker,LocaleProvider } from 'antd'
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import '../../style/statistics/cusrate.scss'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {Link} from 'react-router-dom'
import  $ from 'jquery'
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
const { DataView } = DataSet;
const { Html } = Guide;
class cusRate extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],
          zong:[],
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        var num=1;
        if(loginid!='' && user!=""){
          axios.post('/index.php/user/bing',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.n);
            var num1=res.data.n
            var  zongdata=0;
            num1.map((m,n)=>{
                  zongdata+=m.count;
                  console.log(zongdata);
            })
            this.setState({
              data:res.data.n,
               zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }

      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current")
        var num=e.target.getAttribute('data-key');
        console.log(num);
        if(loginid!='' && user!=""){
          axios.post('/index.php/user/bing',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.n);
            // var  zongdata=0;
            // num1.map((m,n)=>{
            //       zongdata+=m.count;
            //       console.log(zongdata);
            // })
            this.setState({
              data:res.data.n,
              // zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      render(){
        const { zong  } =this.state;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="cusrate">
          <Breadcrumb>
              <Breadcrumb.Item ><Link to="/statistics/cusrate"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item><Link to="/statistics/cusrate"> 客户利用率</Link> </Breadcrumb.Item>
          </Breadcrumb>
              <div className="option">
               
                    <ul className="time">
                    <li> <p  className="title" style={{ marginRight:'660px',fontWeight:400    ,color:'rgba(51,51,51,1)'}}>客户利用率</p></li>
                        <li onClick={this.selectDate.bind(this)} data-key={'1'}>周</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'2'}>月</li>
                        <li onClick={this.selectDate.bind(this)} data-key={'3'}>季度</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'4'}>年度</li>
                    </ul>
          <RangePicker  onChange={onChangeDate} style={{width:400,marginRight:80}} />
        </div>
        <Chart
    height={500}
    width={ 1025 }
    data={this.state.dvv}
    scale={this.state.colss}
    padding={[15, 0, 0, -300]}
    forceFit={false}
      >
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
    position="right-top"
    offsetX={-250}
    offsetY={100}
    textStyle={{  fontSize: '14',}}
    />
    <Tooltip
    showTitle={false}
    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
    <Html
      position={["50%", "50%"]}
      html={(xScale, yScale) => {
        console.log(yScale);
        return `<div>总进客次${zong}个</div>`; 
      }}
      alignX="middle"
      alignY="middle"
    />
    </Guide>
    <Geom
    type="intervalStack"
    position="percent"
    color="item"
    tooltip={[
      "item*percent",
      (item, percent) => {
        percent = percent * 100 + "%";
        return {
          name: item,
          value: percent
        };
      }
    ]}
    style={{
      lineWidth: 3,
      stroke: "#fff"
    }}
    >

    </Geom>
    <Link to="/customermanage/sawcustomer"  style={{ marginLeft:1000,color:'rgba(24,144,255,1)',fontSize:'20px',display:'block'}}>查看详情</Link>
    </Chart>
   
          </div>
          </LocaleProvider>
          )
      }
}
  export default cusRate