import React, { Component } from 'react';
import { Form, Input, DatePicker,LocaleProvider } from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
const { TextArea } = Input;

class modeform extends Component {
  constructor(props) {
    super(props)
    this.state = {
      houDetail:[],
    }
  }

  componentWillReceiveProps(nextProps) {
    console.log(nextProps)
    this.setState({
      houDetail:nextProps.data
    })
  }
  componentDidMount() {

  }


  render() {
    const { getFieldDecorator } = this.props.form;
    const { houDetail } =this.state;
    // const formItemLayout = {
    //   labelCol: {
    //     span: 3
    //   },
    //   wrapperCol: {
    //     span: 19
    //   }
    // }
    return (
        <LocaleProvider locale={zhCN}>
      <div className="news">
        <Form >
          <Form.Item label="客户成交动作" >
            {getFieldDecorator('action', {
               initialValue: houDetail.action
            })(
               <Input  placeholder="送客户小礼物"  style={{width:394}} />,
            )}
          </Form.Item>
          <Form.Item label="客户转公客介绍客户量" >
            {getFieldDecorator('content', {
               initialValue: houDetail.content
            })(
              <TextArea rows={4}  style={{width:394}} placeholder="介绍房源"/>,
            )}
          </Form.Item>
          <Form.Item label="客户再次购买情况" >
            {getFieldDecorator('again', {
              rules: [{ required: false, message: '请输入跟进内容!' }],
               initialValue: houDetail.again
            })(
              <TextArea rows={4}  style={{width:394}}/>,
            )}
          </Form.Item>
        </Form>
      </div>
      </LocaleProvider>
    )
  }
}
const modeforms = Form.create()(modeform)
export default modeforms