import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Menu, 
 Dropdown, } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
class staffForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          total:[],
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
          }
        });
      };
      handleSelectChange(){

      }
      tiaolink(num){
        this.props.history.push({pathname:'/projectmanage/projectInformation/projectadd',data:num});
      }
      componentDidMount(){
        var key=this.props.location.data;
        // console.log(key)
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        //项目列表
        if(loginid!='' && user!=""){
        axios.post('/index.php/dai/tonglist',{
          name:user,  
          num:loginid,
          ids:key,
          y:1,
          n:10,
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
              console.log(res);
             var number=res.data.data;
             if(number.length>0){
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     yuetime:v.time,
                     projectname:v.project,
                     kehuname:v.name,
                     kanfang:v.bei,
                     yeperson:v.staff,
                     part:v.department,
                     rtime:v.deng,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false,
                  total:res.data.total
                 })
             })
            }else{
              this.setState({
                datanum:'',
                loading:false,
                total:res.data.total
               })
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
     //区域数据
    

      }
      // tiaolink(){
      //   this.props.history.push('/personmanage/addstaff');
      // }
      changePage=(current,page )=>{
        var key=this.props.location.data;
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
          axios.post('/index.php/dai/tonglist',{
            num:loginid,
            name:user,
            y:current,
            n:page,
            ids:key
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
                console.log(res);
               var number=res.data.data
                  // console.log(number);
                  var array=[];
                  if(number.length>0){
               
               number.map((v,j)=>{
                array.push({
                    key:v.id,
                    yuetime:v.time,
                    projectname:v.project,
                    kehuname:v.name,
                    kanfang:v.bei,
                    yeperson:v.staff,
                    part:v.department,
                    rtime:v.deng,
                   })
                   this.setState({
                    datanum:array,
                    loading:false,
                    total:res.data.total
                   })
               })
                      
              }else{
                this.setState({
                  datanum:'',
                  loading:false,
                 })
              }
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
            console.log(current,page);
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '约看时间',
            dataIndex: 'yuetime',
          },
          {
            title: '项目名称',
            dataIndex: 'projectname',
          },
          {
            title: '客户姓名',
            dataIndex: 'kehuname',
          },
          {
            title: '看房反馈',
            dataIndex: 'kan',
            render:(record,text)=>{
              return(<span>点击左侧+号看反馈</span>)
                
            }
          },
          {
            title: '业务员',
            dataIndex: 'yeperson',
          },
          {
            title: '部门',
            dataIndex: 'part',
          },
          {
            title: '登记时间',
            dataIndex: 'rtime',
          },
        ];
     
      
      const expandedRowRender = record => <p>{record.kanfang}</p>;
      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          // console.log(count);
          // return '第' + count + '页' + '/' + total + ' 条数据';
           return `共 ${total} 条`;
        }),
        total: this.state.total,
        onChange: (current,page) => this.changePage(current,page),
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item href="/"> 统计分析</Breadcrumb.Item>
                  <Breadcrumb.Item href="">带看量 </Breadcrumb.Item>
                  <Breadcrumb.Item href="">项目带看量 </Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="状态">
              {getFieldDecorator('status', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Select
                style={{ width:'150px',marginRight:40}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="有效">有效</Option>
                  <Option value="住宅">住宅</Option>
                  <Option value="商铺">商铺</Option>
                  <Option value="写字楼">写字楼</Option>
                </Select>,
              )}
            </Form.Item>  

            <Form.Item label="项目名称" layout="inline">
            {getFieldDecorator('address1', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select
                 style={{width:"150px",marginRight:"40px"}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="type1">众安嘉润公馆</Option>
                  <Option value="type2">众安嘉润公馆</Option>
                </Select>,
              )}
            </Form.Item>
            <Form.Item label="客户姓名" layout="inline">
            {getFieldDecorator('part', {
                rules: [{ required: false, message: '请输入客户姓名' }],
              })(
                <Input placeholder="请输入客户姓名"/>
              )}
              
              </Form.Item>
           
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table  columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading}  expandedRowRender={record => <p style={{ lineHeight:2 }}>{record.kanfang}</p>}/>
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginBottom:'17px'}} >删除</Button>
                     <Button type="primary"  style={{width:"100px",height:"32px",}} onClick={this.tiaolink.bind(this)} ><Icon type="plus" />新增项目</Button>
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const staffList = Form.create()(staffForm);
export default staffList