import React ,{Component} from 'react';
import {
    Chart,
    Geom,
    Axis,
    Tooltip,
    Coord,
    Legend,
    Guide,
    Label,
    View,
    Shape,
    Facet,
    Util,
    G2,
   
  } from 'bizcharts';
import {Modal,Table,LocaleProvider,Button} from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import DataSet from "@antv/data-set";
import axios from 'axios'
import EditableFormTable from '../editTable'
import EditMode  from './editMode/editmode'
import echarts from 'echarts/lib/echarts';
import  'echarts/lib/chart/funnel';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
const { Text } = Guide;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
     //饼图1
     const { DataView } = DataSet;
     const { Html } = Guide;
     const data1 = [
       {
         item: "1-3天     |     25%     17人",
         count: 17
       },
       {
         item: "4-6天     |     23%     15人",
         count: 15
       },
       {
         item: "7-11天   |     24%     16人",
         count: 16
       },
       {
         item: "11天+    |     18%    12人",
         count: 12
       }
     ];
     const data2 = [
       {
         item: "新客带看    |     24%     55人",
         count: 55
       },
       {
         item: "老客带看    |     76%     175人",
         count: 175
       },
     ];
     const data3 = [
       {
         item: "私客量     |     25%     17人",
         count: 17
       },
       {
         item: "公客量     |     23%     15人",
         count: 15
       },
       {
         item: "无效客     |     24%     16人",
         count: 16
       },
     
     ];
   
    //  const dv = new DataView();
    //  dv.source(data).transform({
    //    type: "percent",
    //    field: "count",
    //    dimension: "item",
    //    as: "percent"
    //  });
     const dv1 = new DataView();
     dv1.source(data1).transform({
       type: "percent",
       field: "count",
       dimension: "item",
       as: "percent"
     });
     const dv2 = new DataView();
     dv2.source(data2).transform({
       type: "percent",
       field: "count",
       dimension: "item",
       as: "percent"
     });
     const dv3 = new DataView();
     dv3.source(data3).transform({
       type: "percent",
       field: "count",
       dimension: "item",
       as: "percent"
     });

   
     const cols2 = {
       percent: {
         formatter: val => {
           val = val * 200 + "%";
           return val;
         }
       }
     };
     const cols3 = {
       percent: {
         formatter: val => {
           val = val * 200 + "%";
           return val;
         }
       }
     };
     const cols4 = {
       percent: {
         formatter: val => {
           val = val * 200 + "%";
           return val;
         }
       }
     };


class Home extends Component{
      constructor(props){
        super(props)
        this.state={
           notice:[],
           data:[],
          zong:[],

           dvv:[],
           colss:[],

           visible: false ,
           tableData:[],
           lou:[],
           type:[],


           daikan:[],
           daicol:[],
           
           follow:[],
           follCol:[],
           follzong:[],
         
           jin:[],
           jinCol:[],
           zongjin:[],
        }
      }

      showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      handleOk = e => {
        let value=this.editForm.props.form.getFieldsValue();
        console.log(value)
        e.preventDefault();
        this.setState({
          visible: false,
        });

      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
      componentDidMount(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
          axios.get('https://www.easy-mock.com/mock/5cf520e492cde15aa38040f5/example/notic',{
            // name:user,
            // num:loginid
          })
          .then((res)=>{
            console.log(res.data.data.data)
            this.setState({
              notice:res.data.data.data
            })
            
          }).catch((error)=>{
            console.log(error)
          })
        }

        //饼图1数据
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.get('https://www.easy-mock.com/mock/5cf520e492cde15aa38040f5/example/pie',{
          //   name:user,
          // num:loginid
          })
          .then((res)=>{
            this.setState({
              data:res.data.data.data
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
    


  if(loginid!='' && user!=""){
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
    axios.post('/index.php/index/lou',{
       name:user,
       num:loginid
    })
    .then((res)=>{
      if(res.data.code==200){
        console.log(res.data.data);
        var num=res.data.data;
        var array=[];
        var type=[];
        if(num.length>0){
        num.map((m,n)=>{
              type.push(m.action);
              array.push({
                'value':m.pv,
                'name' :m.action
              })

        })
        console.log(type);
         this.setState({
             lou:array,
             type:type
         })
         this.getLouDouData();
      }
    }
    }).catch((error)=>{
      console.log(error)
    })
  }


  this.getDaiKan();
  this.getfollowCycle();
  this.getJinKe();
  this.getImportCus();
      }
      getImportCus=()=>{
           if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            axios.post('/index.php/index/zhong',{
                  name:user,
                  num:loginid
            })
            .then((res)=>{
                console.log(res)
                var num=res.data.data;
                var array=[];
                if(num.length>0){
                  num.map((item,index)=>{
                    array.push({
                      key:item.id,
                      tag:item.tag,
                      name:item.name,
                      project:item.project,
                      origin:item.port,
                      time:item.update_time,
                      gtime:item.g_time,
                      date:item.t_time,
                      registrant:item.building_people,
                      client:item.s_id,
                    });
                   
                  })
               
                  this.setState({
                    tableData:array
                  })
                }
            }).catch((error)=>{
              console.log(error)
            })
          }
    }




      getDaiKan=()=>{
        var num=4
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/dai/utong',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.n);
            var num=res.data.n
            if(num){
            var number1=JSON.parse(res.data.n[0]);
            var number2=JSON.parse(res.data.n[1]);
            console.log(number1,number2);
            var num1=[];
            num1.push(number1,number2);
            console.log(num1);
            var  zongdata=0;
            if(num1.length>0){
            num1.map((m,n)=>{
                  // console.log(m.count);
                  zongdata+=m.count;
                  console.log(zongdata);
            })
          }
            this.setState({
              data:num1,
              zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               daikan:dv,
               daicol:cols
            })
          }
          }).catch((error)=>{
            console.log(error)
          })
        }
        
      }


      getfollowCycle=()=>{
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/gen/tong',{
            name:user,
            num:loginid
          })
          .then((res)=>{
            console.log(res.data.ids.four);
            var number1=JSON.parse(res.data.n[0]);
            var number2=JSON.parse(res.data.n[1]);
            var number3=JSON.parse(res.data.n[2]);
            var number4=JSON.parse(res.data.n[3]);
            console.log(number1,number2,number3,number4);
            var num1=[];
            num1.push(number1,number2,number3,number4);
            console.log(num1);
            var  zong=0;
            if(num1.length>0){
            num1.map((m,n)=>{
                 zong+=m.count
            })
          }
            console.log(zong);
            this.setState({
              data:num1,
              follzong:zong
            })
             const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
             const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
              follow:dv,
              follCol:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
       getJinKe=()=>{
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              axios.post('/index.php/index/jinfen',{
                name:user,
                num:loginid
              })
              .then((res)=>{
                console.log(res.data.data);
                var num=res.data.data;
                var arr=[];
                if(num.length>0){
              num.map((m,n)=>{
                 arr.push({
                    "item":m.port,
                    "count":m.total
                 })
              })
                var  zongjin=0;
                arr.map((m,n)=>{
                  zongjin+=m.count
                })
                console.log(zongjin);
                this.setState({
                  data:arr,
                  zongjin:zongjin
                })
                const dv= new DataView();
                dv.source(this.state.data).transform({
                  type: "percent",
                  field: "count",
                  dimension: "item",
                  as: "percent"
                });
                const cols = {
                  percent: {
                    formatter: val => {
                      val = val * 100 + "%";
                      return val;
                    }
                  }
                };
                this.setState({
                  jin:dv,
                  jinCol:cols
                })
              }
              }).catch((error)=>{
                console.log(error)
              })
              }  
                
       }

        

      getLouDouData=()=>{
        var myChart = echarts.init(document.getElementById('main'));

        var  option = {
           tooltip: {
               trigger: 'item',
               formatter: "{a} <br/>{b} : {c}%"
           },
           toolbox: {
               feature: {
                   dataView: {readOnly: false},
                   restore: {},
                   saveAsImage: {}
               }
           },
           legend: {
               data:  this.state.type
           },
           calculable: true,
           color: [ '#3fa1fd', '#5db6fd', '#7fc8fe', '#c3eafe', '#2c6abc',],
           series: [
               {
                   name:'漏斗图',
                   type:'funnel',
                   left: '10%',
                   //x2: 80,
                   bottom: 60,
                   width: '80%',
                   // height: {totalHeight} - y - y2,
                   min: 0,
                   max: 100,
                   minSize: '0%',
                   maxSize: '100%',
                   sort: 'descending',
                   gap: 2,
                   label: {
                       show: true,
                       position: 'inside'
                   },
                   labelLine: {
                       length: 10,
                       lineStyle: {
                           width: 1,
                           type: 'solid'
                       }
                   },
                   itemStyle: {
                       borderColor: '#fff',
                       borderWidth: 1
                   },
                   emphasis: {
                       label: {
                           fontSize: 20
                       }
                   },
                   data:this.state.lou
                   
                   
               }
           ]
       };
       
       myChart.setOption(option)
       
      }
      tiaoDetail(num){
        this.props.history.push({pathname:'/customermanage/customerdetail',data:num});

      }

      render(){
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper:{goButton:<Button type="primary">确定</Button>},
          showTotal: ((total) => {
            return `共 ${total} 条`;
          }),
          //  current: parseInt(this.state.pageNum),
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          //  onShowSizeChange: (current,pageSize) =>{
          //        console.log(current,pageSize);
          //  },
          // onChange: (current) => this.changePage(current),
        };
        const { Text } = Guide;
        const cols5 = {
          percent: {
            nice: false
          }
        };
        const { lou, col5,follow,follCol,follzong,jin,jinCol,zong,zongjin} =this.state;
      const columns = [
          {
            title: '标签',
            dataIndex: 'tag',
          },
          {
            title: '客户姓名',
            dataIndex: 'name',
          },
          {
            title: '进客项目',
            dataIndex: 'project',
          },
          {
            title: '进客来源',
            dataIndex: 'origin',
          },
          {
            title: '登记时间',
            dataIndex: 'time',
          },
          {
            title: '最近跟进时间',
            dataIndex: 'gtime',
          },
          {
            title: '提醒日期',
            dataIndex: 'date',
          },
          {
            title: '登记人',
            dataIndex: 'registrant',
          },
          {
            title: '委托人',
            dataIndex: 'client',
          },
          {
            title: '操作',
            dataIndex: 'handel',
            render:(text,record)=> <a  href="javascript:;" style={{ color:'rgba(24,144,255,1)'}} onClick={this.tiaoDetail.bind(this,record.key)}>编辑</a>
            
          },
        ];
        return(
          <LocaleProvider locale={zhCN}>
          <div className="Home">
                     

        <div className="my-content" style={{ background: '#fff', minHeight:'51.8vh' ,minWidth:  '84.3vw',borderRadius:'12px' }}>
       {/*进客量饼状图 */}
      
  <div className="leftPie">
       <Chart
    height={200}
    width={ 525 }
    data={jin}
    scale={jinCol}
    padding={[15, 0, 0, -300]}
    forceFit={false}
      >
         <p className="title">进客量</p>
<Coord type={"theta"} radius={0.75} innerRadius={0.7} />
<Axis name="percent" />
<Legend
position="right"
offsetX={-300}
offsetY={-30}
textStyle={{  fontSize: '14',}}
/>
<Tooltip
showTitle={false}
itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
/>
<Guide>
<Html
  position={["50%", "50%"]}
  html={(xScale, yScale) => {
    return `<div>进客量${zongjin}人</div>`; 
  }}
  alignX="middle"
  alignY="middle"
/>
</Guide>
<Geom
type="intervalStack"
position="percent"
color="item"
tooltip={[
  "item*percent",
  (item, percent) => {
    percent = percent * 100 + "%";
    return {
      name: item,
      value: percent
    };
  }
]}
style={{
  lineWidth: 3,
  stroke: "#fff"
}}
>

</Geom>
</Chart>
      {/*客户跟进周期饼状图*/}
   <Chart
         height={200}
         width={525}
         data={follow}
         scale={follCol}
        forceFit={false}
       padding={[15,0,0,-300]}
  >
     <p className="title">客户跟进周期</p>
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
      position="right"
      offsetY={-30}
      offsetX={-300}
      textStyle={{  fontSize: '14',}}
    />
    <Tooltip
      showTitle={false}
      itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}:{value}</li>"
    />
    <Guide>
      <Html
        position={["50%", "50%"]}
        html={(xScale, yScale) => {
          return `<div>私客量${follzong}人</div>`; 
        }}
        alignX="middle"
        alignY="middle"
      />
    </Guide>
    <Geom
      type="intervalStack"
      position="percent"
      color="item"
      tooltip={[
        "item*percent",
        (item, percent) => {
          percent = percent * 100 + "%";
          return {
            name: item,
            value: percent
          };
        }
      ]}
      style={{
        lineWidth: 3,
        stroke: "#fff"
      }}
    >
     
    </Geom>
  </Chart>
  </div>

  
    <div className="midMap">
      <p style={{fontSize:20,marginLeft:150,color:'rgba(50,50,50,1)' }}>漏斗分析</p>
      {console.log(this.state.lou,this.state.col5 )}
      
      <div id="main" style={{ width: 400, height: 500 }}></div>







    </div>
    {/** 带看量*/}
    <div className="rigPie">
    <Chart
         height={200}
         width={525}
         data={this.state.daikan}
         scale={this.state.daicol}
        forceFit={false}
       padding={[15,0,0,-300]}
  >
     <p className="title">带看量</p>
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
      position="right"
      offsetY={-30}
      offsetX={-300}
      textStyle={{  fontSize: '14',}}
    />
    <Tooltip
      showTitle={false}
      itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
      <Html
        position={["50%", "50%"]}
        html={(xScale, yScale) => {
          return `<div>总带看${zong}人</div>`; 
        }}
        alignX="middle"
        alignY="middle"
      />
    </Guide>
    <Geom
      type="intervalStack"
      position="percent"
      color="item"
      tooltip={[
        "item*percent",
        (item, percent) => {
          percent = percent * 100 + "%";
          return {
            name: item,
            value: percent
          };
        }
      ]}
      style={{
        lineWidth: 3,
        stroke: "#fff"
      }}
    >
     
    </Geom>
  </Chart>
  <Chart
         height={200}
         width={525}
         data={dv3}
         scale={cols4}
        forceFit={false}
       padding={[15,0,0,-300]}
  >
     <p className="title">客户回访量</p>
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
      position="right"
      offsetY={-30}
      offsetX={-300}
      textStyle={{  fontSize: '14',}}
    />
    <Tooltip
      showTitle={false}
      itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
      <Html
        position={["50%", "50%"]}
        html={(xScale, yScale) => {
          return `<div>私客${zong}人</div>`; 
        }}
        alignX="middle"
        alignY="middle"
      />
    </Guide>
    <Geom
      type="intervalStack"
      position="percent"
      color="item"
      tooltip={[
        "item*percent",
        (item, percent) => {
          percent = percent * 100 + "%";
          return {
            name: item,
            value: percent
          };
        }
      ]}
      style={{
        lineWidth: 3,
        stroke: "#fff"
      }}
    >
     
    </Geom>
  </Chart>
    </div>
 
</div>
<div className="import-cus">
    <div className="customer">
          <h1 className="title">重点客户</h1>
          
          <Table
          bordered={false}
          dataSource={this.state.tableData}
          columns={columns}
          style={{ marginRight:15}}
          pagination={ paginationProps } 
        />
          {/* <p>共&nbsp;&nbsp;400&nbsp;&nbsp;条&nbsp;&nbsp;第&nbsp;3&nbsp;页</p> */}

         
  <Modal
  title="客户编辑"
  visible={this.state.visible}
  onOk={this.handleOk}
  onCancel={this.handleCancel}
>
  <EditMode wrappedComponentRef={(inst)=>{this.editForm= inst;}}/>
</Modal>
    </div>
    <div className="notice">
        <h1 className="title">公告</h1>
          <div className="public-notice">
            <ul>
              {
                this.state.notice.length>0?this.state.notice.map((item,index)=>{
                        return(
                          <li key={index}>
                              <span>{item.time}</span>
                              <i>{item.name}</i>
                           </li>
                        )
                }): '暂无数据'
              }
            
            </ul>
          </div>

    </div>
</div>

          </div>
          </LocaleProvider>
          )
      }
}
  export default Home