import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,

} from 'antd';
import '../../../style/addtuangou.scss'
import axios from 'axios'
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
              detail:[],
        }
        
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!=='' && user!==""){
            console.log(key);
            axios.post('/index.php/tuan/save',{
              bid:key,
              value:values,
              name:user,
              num:loginid
         }).then((res)=>{
                if(res.data.code==200){
                 console.log('提交团购成功')
               }
         }).catch((error)=>{
               console.log(error)
         })
        }

          }
        });
      };
      getDetail=()=>{
        var key=sessionStorage.getItem('key');
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!=='' && user!==""){
          console.log(key);
          axios.post('/index.php/tuan/index',{
            id:key,
            name:user,
            num:loginid
       }).then((res)=>{
              if(res.data.code==200){
                  console.log(res.data.data);
                  var num=res.data.data;
                  if(num.length>0){
                     this.setState({
                        detail:res.data.data[0]
                      })
                  }
                 
                  console.log(this.state.detail);
             }
       }).catch((error)=>{
             console.log(error)
       })
      }


      }
      componentDidMount(){
        this.getDetail();
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { detail } =this.state;
        return(
    <div className="addtuangou">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
            <Breadcrumb.Item href="">编辑团购 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">团购标准：</p>
            <Form.Item >
            {getFieldDecorator('title', {
              initialValue:detail.title,
                })(
            <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} />,
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">开始时间：</p>
            <Form.Item >
            {getFieldDecorator('start', {
               initialValue: detail.start,
                })(
            <Input rows={4}  style={{width:'1000px'}} />
            )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">结束时间：</p>
            <Form.Item >
            {getFieldDecorator('end', {
               initialValue: detail.end,
                })(
            <Input rows={4}  style={{width:'1000px'}}/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
              更新
          </Button>
          {/* <Button type="primary" htmlType="submit" className="sub">
              删除
          </Button> */}
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou