  function countDown() {
    let secondsToGo = 5;
    const modal = Modal.success({
      title: '提交成功',
      content: ` ${secondsToGo}秒后消失`,
    });
     // <Link to='/projectmanage/projectInformation/projectadd'><Button type="primary">返回列表</Button></Link>
    // <Link to='/projectmanage/projectInformation/projectlist'> <Button>查看项目</Button></Link>
    const timer = setInterval(() => {
      secondsToGo -= 1;
      modal.update({
        content: ` ${secondsToGo} 秒后消失`,
      });
    }, 1000);
    setTimeout(() => {
      clearInterval(timer);
      modal.destroy();
    }, secondsToGo * 1000);
  }
  function error() {
    Modal.error({
      title: 'error',
      content: '数据提交失败，请重新提交',
    });
  }
