import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker, LocaleProvider} from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import  '../../../style/statistics/cusfollowcycle.scss'
import   { Link } from 'react-router-dom'
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
const { DataView } = DataSet;
const { Html } = Guide;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class cusFollowCycle extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],
          zong:[],
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
        axios.post('/index.php/gen/tong',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          // if(res.data.ids.four){
          //      this.setState({
          //        type:res.data.ids.four
          //      })
          // }
          console.log(res.data.ids.four);
          var number1=JSON.parse(res.data.n[0]);
          var number2=JSON.parse(res.data.n[1]);
          var number3=JSON.parse(res.data.n[2]);
          var number4=JSON.parse(res.data.n[3]);
          console.log(number1,number2,number3,number4);
          var num1=[];
          num1.push(number1,number2,number3,number4);
          console.log(num1);
          var  zong=0;
          num1.map((m,n)=>{
               zong+=m.count
          })
          console.log(zong);
          this.setState({
            data:num1,
            zong:zong
          })
           const dv= new DataView();
          dv.source(this.state.data).transform({
            type: "percent",
            field: "count",
            dimension: "item",
            as: "percent"
          });
           const cols = {
            percent: {
              formatter: val => {
                val = val * 100 + "%";
                return val;
              }
            }
          };
          this.setState({
             dvv:dv,
             colss:cols
          })
        }).catch((error)=>{
          console.log(error)
        })
      }
      }
      tiaodetail=(e)=>{
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
            console.log(e.target);
            var num=e.target.getAttribute('data-id');
            console.log(num);
             if(num==1){
              if(loginid!='' && user!=""){
                axios.post('/index.php/gen/tong',{
                  name:user,
                  num:loginid
                })
                .then((res)=>{
                  console.log(res);
                   localStorage.setItem('tiaoid',res.data.ids.one);
                   var tiaoid=localStorage.getItem('tiaoid');
                  console.log(tiaoid)
                  this.props.history.push({pathname:'/statistics/cusfollowdetail',data:tiaoid});
             }).catch((error)=>{
                 console.log(error);
             })
            }
            
          
          }else if(num==2){
            if(loginid!='' && user!=""){
              axios.post('/index.php/gen/tong',{
                name:user,
                num:loginid
              })
              .then((res)=>{
                console.log(res);
                 localStorage.setItem('tiaoid',res.data.ids.two);
                 var tiaoid=localStorage.getItem('tiaoid');
               console.log(tiaoid)
               this.props.history.push({pathname:'/statistics/cusfollowdetail',data:tiaoid});
           }).catch((error)=>{
               console.log(error);
           })
          }
          }else if(num==3){
            if(loginid!='' && user!=""){
              axios.post('/index.php/gen/tong',{
                name:user,
                num:loginid
              })
              .then((res)=>{
                console.log(res);
                 localStorage.setItem('tiaoid',res.data.ids.three);
                 var tiaoid=localStorage.getItem('tiaoid');
               console.log(tiaoid)
               this.props.history.push({pathname:'/statistics/cusfollowdetail',data:tiaoid});
           }).catch((error)=>{
               console.log(error);
           })
          }
          }else if(num==4){
            if(loginid!='' && user!=""){
              axios.post('/index.php/gen/tong',{
                name:user,
                num:loginid
              })
              .then((res)=>{
                console.log(res);
                 localStorage.setItem('tiaoid',res.data.ids.four);
               var tiaoid=localStorage.getItem('tiaoid');
               console.log(tiaoid)
               this.props.history.push({pathname:'/statistics/cusfollowdetail',data:tiaoid});
           }).catch((error)=>{
               console.log(error);
           })
          }



          }
             
      }
      render(){
        const { zong  } =this.state;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="cusfollowcycle">
              <Breadcrumb>
              <Breadcrumb.Item  ><Link to="/statistics/cusfollowcycle"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item ><Link to="/statistics/cusfollowcycle"> 客户跟进周期 </Link></Breadcrumb.Item>
          </Breadcrumb>
              <div className="option">
                <span className="title">客户回访量</span>
              <RangePicker onChange={onChangeDate} style={{width:400,marginLeft:80}} />
                   
                   
                   
        </div>
        <Chart
    height={500}
    width={ 1000}
    data={this.state.dvv}
    scale={this.state.colss}
    padding={[15, 0, 0, -200]}
    forceFit={false}
    style={{float:'left'}}
      >
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
    position="right-top"
    offsetX={-200}
    offsetY={100}
    textStyle={{  fontSize: '14',}}
    />
    <Tooltip
    showTitle={false}
    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
    <Html
      position={["50%", "50%"]}
      html={(xScale, yScale) => {
        console.log(yScale);
        return `<div>总客户量${zong}个</div>`; 
      }}
      alignX="middle"
      alignY="middle"
    />
    </Guide>
    <Geom
    type="intervalStack"
    position="percent"
    color="item"
    tooltip={[
      "item*percent",
      (item, percent) => {
        percent = percent * 100 + "%";
        return {
          name: item,
          value: percent
        };
      }
    ]}
    style={{
      lineWidth: 3,
      stroke: "#fff"
    }}
    >

    </Geom>
    </Chart>
               <ul style={{cursor:'pointer'}} className='detail'>
                        <li  className="detail1" style={{color:'rgba(24,144,255,1)'}} onClick={this.tiaodetail.bind(this)} data-id={'1'}>1-3详情</li>
                        <li className="detail2" style={{color:'rgba(24,144,255,1)'}} onClick={this.tiaodetail.bind(this)}   data-id={'2'}>4-6详情</li>
                       <li  className="detail2" style={{color:'rgba(24,144,255,1)'}} onClick={this.tiaodetail.bind(this)}  data-id={'3'}>7-11详情</li>
                       <li  className="detail2" style={{color:'rgba(24,144,255,1)'}} onClick={this.tiaodetail.bind(this)}  data-id={'4'}>11+详情</li>
                   </ul>
          </div>
        </LocaleProvider>
          )
      }
}
  export default cusFollowCycle