import React ,{Component} from 'react';
import { Form, Input, 
  Button, 
  Breadcrumb ,
  DatePicker,
  LocaleProvider,
  Select,
  Popconfirm,
 message } from 'antd'
import '../../style/statistics/total.scss'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios'
import $ from 'jquery'
import { Link } from 'react-router-dom'
const {Option} = Select;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;

// function confirm(e) {
//   console.log(e);
//   message.success('导出成功');
// }

function cancel(e) {
  console.log(e);
  message.error('取消导出');
}

class TotalForm extends Component{
      constructor(props){
        super(props)
        this.state={
              total:[],
              quyu:[],
              rengetime:[],
        }
      }
      handleSubmit = e => {
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var type= localStorage.getItem('data_type');
            if(loginid!='' && user!=""){
              axios.post('/index.php/index/sou',{
                name:user,
                num:loginid,
                id:values.quyu,
                type:type
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  // console.log(res.data.list);
                   this.setState({
                    total:res.data.data
                  })
                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
    
            }
          }
        });
      };
      componentDidMount(){
        this.getarea();
        // if(loginid!='' && user!=""){
        //   axios.post('/index.php/index/index',{
        //     name:user,
        //     num:loginid
        //   })
        //   .then((res)=>{
        //     if(parseInt(res.status) === 200){
        //       console.log(res);
        //     }else{
        //        console.log("请求数据失败")
        //     }
        //   }).catch((error)=>{
        //     console.log(error)
        //   })

        // }



      }

      getarea=()=>{
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
          axios.post('/index.php/index/list',{
            name:user,
            num:loginid,
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log(res.data.list);
              this.setState({
                 quyu:res.data.list
              })
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })

        }
      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current");
          console.log(e.target);
          var num=e.target.getAttribute('data-key');
          localStorage.setItem('data_type',num);
           console.log(num);
          if(loginid!='' && user!=""){
            axios.post('/index.php/index/index',{
              name:user,
              num:loginid,
              type:num
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                console.log(res.data.data);
                this.setState({
                   total:res.data.data
                })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
  
          }
  
      }
      confirm=(e)=>{
        var html = "<html><head><meta charset='utf-8' /></head><body>" + document.getElementsByTagName("table")[0].outerHTML + "</body></html>";
        console.log(html);
        console.log(e.target);
        // 实例化一个Blob对象，其构造函数的第一个参数是包含文件内容的数组，第二个参数是包含文件类型属性的对象
        var blob = new Blob([html], { type: "application/vnd.ms-excel" });
        var a = e.target
        // 利用URL.createObjectURL()方法为a元素生成blob URL
        a.href = URL.createObjectURL(blob);
        // 设置文件名
        a.download = "总数据.xls";
      }



      onChangeDate=(date, dateString) =>{
        console.log(date, dateString);
        console.log(this);
        this.setState({
          rengetime:dateString
        })

      }
      cha=()=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
           console.log(this.state.rengetime);
           var time=this.state.rengetime;
           if(loginid!='' && user!=""){
            axios.post('/index.php/index/index',{
              name:user,
              num:loginid,
              type:6,
              time:time
            })
            .then((res)=>{
              if(res.data.code== 200){
                  console.log(res.data.data);
                  this.setState({
                    total:res.data.data
                 })



              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
  
          }

      }
      render(){
        const { total ,quyu } =this.state;
        const { getFieldDecorator } = this.props.form;
        return(
          <LocaleProvider locale={zhCN}>
      <div className="total">
        <div className="bread">
    <Breadcrumb>
        <Breadcrumb.Item  ><Link to="/statistics/total"> 统计分析</Link></Breadcrumb.Item>
        <Breadcrumb.Item ><Link to="/statistics/total"> 总数据 </Link></Breadcrumb.Item>
    </Breadcrumb>
    </div>
    <div className="zongtable"> 
        <Form layout="inline" onSubmit={this.handleSubmit}>
          <Form.Item label="区域">
          {getFieldDecorator('quyu', {
                })(
            <Select rows={4}   style={{ width:150,marginRight:45}} placeholder="请选择">
              {
                   quyu.map((m,n)=>{
                       return(
                         <Option value={m.id} key={m.id}>{m.name}</Option>
                       )
                   })
              }
            </Select>
                )}
          </Form.Item>
          <Form.Item >
            <Button type="primary" style={{marginRight:20}} htmlType="submit">查询</Button>
          </Form.Item>
          <Form.Item >
      {/* <Popconfirm
        title="确认导出总数据吗?"
        // onConfirm={this.confirm.bind(this)}
        onCancel={cancel}
        okText="确定"
        cancelText="取消"
      >
           
      </Popconfirm> */}
      <a  style={{marginRight:330,width:64,height:32,backgroundColor:'rgb(24, 144, 255)',color:'#fff',borderRadius:'3px',padding:'6px 17px'}} onClick={this.confirm}>导出</a>
          </Form.Item>
          <Form.Item >
          <ul className="time" style={{cursor:'pointer'}}>
              <li onClick={this.selectDate.bind(this)} data-key={'1'} >日</li>
              <li onClick={this.selectDate.bind(this)} data-key={'2'}>周</li>
              <li onClick={this.selectDate.bind(this)}  data-key={'3'}>月</li>
              <li onClick={this.selectDate.bind(this)} data-key={'4'}>季度</li>
              <li onClick={this.selectDate.bind(this)}  data-key={'5'}>年度</li>
          </ul>
          </Form.Item>
          <Form.Item >
          {/* {getFieldDecorator('shijian', {
                })( */}
                  <RangePicker  onChange={this.onChangeDate} style={{width:400,}}  />
                {/* // )} */}
          </Form.Item>
          <Form.Item >
               <Button type="primary" style={{marginRight:20}} onClick={this.cha.bind(this)}>查询</Button>
          </Form.Item>
        </Form>
        <table border='1'>
          <thead>
            <tr>
              <th colspan='2'></th>
              <th colspan='2' >进客量</th>
              <th colspan='3'>带看量</th>
              <th colspan='3'>跟进量</th>
              <th colspan='4'>客户跟进周期</th>
              <th colspan='2'>成交量</th>
              {/* <th colspan='3'>踩盘量</th> */}
              <th colspan='3'>客户利用率</th>
            </tr>
            <tr>
              <th colspan='2' ></th>
              <th colspan='2' >3</th>
              <th>总带看量</th>
              <th>新客</th>
              <th>老客</th>
              <th>跟进量</th>
              <th>私客</th>
              <th>公客</th>
              <th>1~3</th>
              <th>4~6</th>
              <th>7~10</th>
              <th>11+</th>
              <th>套数</th>
              <th>金额</th>
              {/* <th>项目总量</th>
              <th>踩盘量</th>
              <th>考盘量</th> */}
              <th>总利用</th>
              <th>新客</th>
              <th>老客</th>
            </tr>
          </thead>
          <tbody>
         
            {
                total.map((m,n)=>{
                    return(
                      <tr>
                       <td colspan='2'>{m[0]}</td>
                      <td colspan='2'>{m[1]}</td>
                      <td>{m[2]}</td>
                      <td>{m[3]}</td>
                      <td>{m[4]}</td>
                      <td>{m[5]}</td>
                      <td>{m[6]}</td>
                      <td>{m[7]}</td>
                      <td>{m[8]}</td>
                      <td>{m[9]}</td>
                      <td>{m[10]}</td>
                      <td>{m[11]}</td>
                      <td>{m[12]}</td>
                      <td>{m[13]}</td>
                      <td>{m[14]}</td>
                      <td>{m[15]}</td>
                      <td>{m[16]}</td>
                    </tr>
                    
                    )
                })
            }
             {/* <tr key={n}>
                      <td colspan='2'>{m[0]}</td>
                      <td colspan='2'>{m[1]}</td>
                      <td>{m[2]}</td>
                      <td>{m[3]}</td>
                      <td>{m[4]}</td>
                      <td>{m[5]}</td>
                      <td>{m[6]}</td>
                      <td>{m[7]}</td>
                      <td>{m[8]}</td>
                      <td>{m[9]}</td>
                      <td>{m[10]}</td>
                      <td>{m[11]}</td>
                      <td>{m[12]}</td>
                      <td>{m[13]}</td>
                      <td>{m[14]}</td>
                      <td>{m[15]}</td>
                      <td>{m[16]}</td>
                     </tr> */}
           
           
          </tbody>
        </table>
    </div>

      </div>
      </LocaleProvider>
          )
      }
}

const Total = Form.create()(TotalForm);
export default Total