import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker,Form,Select,Cascader,Input,LocaleProvider } from 'antd'
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import  '../../../style/statistics/volume.scss'
