import React ,{Component} from 'react';
import { 
    Form, 
    Input, 
    Button, 
    Breadcrumb ,
    Col,
    Row,
    Upload,
     Icon, 
     Modal
  } from 'antd';
  import ReactQuill from 'react-quill'
  import 'react-quill/dist/quill.snow.css' 
  import axios from 'axios'
  function getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
          previewVisible: false,
          previewImage: '',
          fileList: [],
          error: '',
          editorHtml: '',
          newsImg:[],

          newsdetail:[],
          main:[],
          
        }
        this.handleChange = this.handleChange.bind(this)
      }
      
      modules = {
        toolbar: [
          [{ 'header': [1, 2, false] }],
          ['bold', 'italic', 'underline','strike', 'blockquote'],
          [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
          ['link', 'image'],
          ['clean']
        ],
      };
    
      formats = [
        'header',
        'bold', 'italic', 'underline', 'strike', 'blockquote',
        'list', 'bullet', 'indent',
        'link', 'image'
      ];
      
     
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('新闻资讯内容为: ', values);
              //新增新闻资讯
              var key=this.props.location.data;
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              var img=this.state.main.concat(this.state.newsImg);
              console.log(loginid,user);
              if(loginid!='' && user!=""){
              axios.post('/index.php/info/nupdate',{
                   content:this.state.editorHtml,
                   title:values.title,
                   img: img,
                   name:user,
                   num:loginid,
                   id:key,
              })
              .then((res)=>{
                    if(res.data.code==200){
                      console.log(res);
                      this.props.history.push('/projectmanage/projectInformation/loupannews');
                    }
              }).catch((error)=>{
                console.log(error)
              })
            }




          }
        });
      };
      componentDidMount(){
          this.getDetail();

      }
      customerRequest = (option)=> {
        var data=getBase64(option.file);
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        console.log(loginid,user);
        if(loginid!='' && user!=""){
         data.then(ress=>{
         axios.post('/index.php/pro/test',{
           data:ress,
           name:user,
           num:loginid
           })
          .then((res)=>{
             if(res.data.code==200){
                var  arr=[];
               arr.push({
                 uid:res.data.url,
                 name:res.data.url,
                 status: 'done',
                 url:`http://${res.data.url}`
              })
              console.log( res.data.url);
              this.setState({
                fileList :arr,
                newsImg:res.data.url,
              })
            }else {
              
            }
          
          }).catch((err)=>{
            console.log(err)
          })
        })
      }
    }

      handleCancel = () => this.setState({ previewVisible: false });
      handlePreview = async file => {
        if (!file.url && !file.preview) {
          file.preview = await getBase64(file.originFileObj);
        }
    
        this.setState({
          previewImage: file.url || file.preview,
          previewVisible: true,
        });
      };
    
      ChangePic = ({ fileList }) => this.setState({ fileList });
     


      handleChange(html) {
        this.setState({  editorHtml: html })
        console.log(html);
      console.log(this.state.editorHtml);
      }

      getDetail=()=>{
        var key=this.props.location.data;
        console.log(key);
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
                axios.post('/index.php/info/nedit',{
                    id:key,
                    name:user,
                    num:loginid
            })
            .then((res)=>{
                  if(res.data.code==200){
                     console.log(res);
                     this.setState({
                           newsdetail:res.data.data,
                           editorHtml:res.data.data.content,
                           main:res.data.data.img
                     })
                     var imgdetail=res.data.data.img;
                     var arr=[];
                     arr.push({
                      uid:imgdetail,
                      name:imgdetail,
                      status: 'done',
                      url:`http://${imgdetail}`,
                     })
                     this.setState({
                      fileList:arr
                     })
                     
                  }
            }).catch((error)=>{
              console.log(error)
            })
          }

  }
  removeHetong=(file)=>{
    var loginid=localStorage.getItem('loginid');
  var user=localStorage.getItem('name');
    console.log(file.url);
    var longstr=file.url;
    var str = longstr.slice(7);
    console.log(str);
    if(loginid!='' && user!=""){
      axios.post('/index.php/recording/delpic',{
        url:str,
        name:user,
        num:loginid
        })
       .then((res)=>{
          if(res.data.code==200){
             console.log(res);
              console.log(this.state.main);
            //   var yuan=this.state.main;
            //  var xian= yuan.remove(str);
            //  console.log(xian);
            this.setState({
              main:''
            })
         }else {
           
         }
       
       }).catch((err)=>{
         console.log(err)
       })
      }

  }
      render(){
        const { newsdetail } =this.state;
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
          <div>
            <Icon type="plus" />
            <div className="ant-upload-text">Upload</div>
          </div>
        );
        return(
          <div className="newszixunadd">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">楼盘资讯 </Breadcrumb.Item>
            <Breadcrumb.Item href="">新闻资讯 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <Form.Item label="资讯标题" >
            {getFieldDecorator('title', {
               initialValue:newsdetail.title
                })(
               <Input placeholder="请输入" style={{ width:750}}/>
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <Form.Item label="资讯内容" > 
          
            {/* {getFieldDecorator('zixun_context', {   content
                })( */}
              <ReactQuill modules={this.modules} formats={this.formats} style={{width:750, height:450 }} onChange={this.handleChange} value={this.state.editorHtml}/> 
              {/* )} */}
            </Form.Item >
        </Row>
        <br/>
        <br/>
        <br/>
          <br/>
        <Row>
          
            <Form.Item label="上传主图">
            {getFieldDecorator('upload_pic', {    //img
                })(
                  <Upload
                  customRequest={this.customerRequest}
                  listType="picture-card"
                  fileList={fileList}
                  onPreview={this.handlePreview}
                   onChange={this.ChangePic}
                   onRemove={this.removeHetong}
                >
                  {fileList.length >= 1 ? null : uploadButton}
                </Upload> )}
                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                <img alt="example" style={{ width: '100%' }} src={previewImage} />
              </Modal>
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          上传
          </Button>
        </Form.Item>
          </Form>
          </div>
          )
      }
}
const newsZixunAdd = Form.create()(RegistForm);
export default newsZixunAdd
