import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Menu, 
 Dropdown, 
Modal} from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const {Option}=Select
const { TabPane } = Tabs;
const { confirm } = Modal;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function showConfirm() {
  confirm({
    title: '确定删除该员工吗?',
    content: '删除员工后不可恢复',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
class staffForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          position:[],
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('查询数据为: ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
            axios.post('/index.php/staff/sou',{ 
              name:user,
              num:loginid,
              value:values
               })
               .then((res)=>{
                 if(res.data.code==200){
                   console.log(res);
                   var number=res.data.data
                   console.log(number);
                   var arr=[];
                   if(number.length>0){
                   number.map((v,j)=>{
                    arr.push({
                           key:v.id,
                           staffname:v.name,
                           sex:v.sex,
                           city:v.city,
                           part:v.department,
                           post:v.job,
                           entrytime:v.r_time,
                           telphone:v.urgent,
                       })
                       this.setState({
                        datanum:arr,
                        loading:false
                       })
                   })
                  }else {
                    this.setState({
                      datanum:'',
                      loading:false
                     })
                  }
                  // this.setState({
                  //   position:res.data.data
                  // })
                 }else {
                 
                 }
               }).catch(error=>{
                console.loog(error)
              })
              }


          }
        });
      };
      handleSelectChange(){

      }
      getPosition(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/role/list',{ 
          name:user,
          num:loginid
           })
           .then((res)=>{
            
             if(res.data.code==200){
              console.log(res.data.data);
              this.setState({
                position:res.data.data
              })
             }else {
             
             }
           }).catch(error=>{
            console.loog(error)
          })
          }
      }
      tiaoDetail(number){
        console.log(number)
        this.props.history.push({pathname:'/personmanage/staffdetail',data:number});
      }
      tiaoEdit(num){
        console.log(num);
        localStorage.setItem('edit_id',num)
        this.props.history.push('/personmanage/staffedit');
        window.location.reload();
        // this.props.history.push({pathname:'/personmanage/staffedit',dataa:num});
      }
      componentDidMount(){
        //项目列表
      this.getstaffList();
      this.getPosition();
     //区域数据
    

      }
      getstaffList(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        axios.post('/index.php/staff/index',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
              
             var number=res.data.data
             console.log(number);
             var arr=[];
             number.map((v,j)=>{
              arr.push({
                     key:v.id,
                     staffname:v.name,
                     sex:v.sex,
                     city:v.city,
                     part:v.department,
                     post:v.job,
                     entrytime:v.r_time,
                     telphone:v.urgent,
                 })
                 this.setState({
                  datanum:arr,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
      }
      tiaolink(){
        this.props.history.push('/personmanage/addstaff');
      }
      deletestaff(num){
          console.log(num);
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/staff/delete',{ 
            id:num,
            name:user,
            num:loginid
             })
             .then((res)=>{
              
               if(res.data.code==200){
                 console.log('删除成功');
                this.getstaffList();
               }else {
               
               }
             }).catch(error=>{
              console.loog(error)
            })
            }
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '员工姓名',
            dataIndex: 'staffname',
          },
          {
            title: '性别',
            dataIndex: 'sex',
          },
          {
            title: '城市',
            dataIndex: 'city',
          },
          {
            title: '部门',
            dataIndex: 'part',
          },
          {
            title: '岗位',
            dataIndex: 'post',
          },
          {
            title: '入职时间',
            dataIndex: 'entrytime',
          },
          {
            title: '电话',
            dataIndex: 'telphone',
          },
          {
            title: '操作',
            dataIndex: 'handle',
            render:(text ,record) =><div> <span onClick={this.tiaoDetail.bind(this,record.key)} style={{color:"#1890FF",cursor:"pointer"}}>详情</span>&nbsp;
                                          <span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.tiaoEdit.bind(this,record.key)}>修改</span> &nbsp;
                                          <span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.deletestaff.bind(this,record.key)}>删除</span>
                                    </div>,
          },

        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          // console.log(count);
           return `共 ${total} 条`;
        }),
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item  > <Link to="/personmanage/stafflist">人员管理</Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/personmanage/stafflist">员工列表 </Link></Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="员工姓名">
              {getFieldDecorator('name', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Input
                style={{ width:'150px',marginRight:40}}
                  placeholder="所有"
                  // onChange={this.handleSelectChange}
                />
              )}
            </Form.Item>  

            <Form.Item label="城市" layout="inline">
            {getFieldDecorator('city', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Input
                 style={{width:"150px",marginRight:"40px"}}
                  placeholder="城市"
                />
              )}
            </Form.Item>
            <Form.Item label="部门" layout="inline">
            {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                 <Input
                  placeholder="部门"
                  style={{width:"150px",marginRight:"40px"}}
                />
              )}
              
              </Form.Item>
            <Form.Item label="岗位" layout="inline">
            {getFieldDecorator('job', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                 <Select
                  placeholder="岗位"
                  onChange={this.handleSelectChange}
                  style={{width:"150px",marginRight:"40px"}}
                >
                   
                   { 
                     this.state.position.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      })
                  }
                </Select> 
              )}
              
              </Form.Item>
             
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginBottom:'12px'}} onClick={showConfirm}>删除</Button>
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'40px'}} onClick={this.tiaolink.bind(this)} ><Icon type="plus" />新增员工</Button>
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const staffList = Form.create()(staffForm);
export default staffList