import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,

} from 'antd';
import '../../../style/addbuyhousehelp.scss'
import axios from 'axios'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
            editText:[]
        }
      }
      handleSubmit = e => {
        var key=this.props.location.data;
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
            axios.post('/index.php/zhi/update',{
              id:key,
              value:values,
              name:user,
              num:loginid
            })
            .then((res)=>{
              console.log(res);
            }).catch((error)=>{
              console.log(error)
            })
          }

          }
        });
      };
      componentDidMount(){
        console.log(this.props.location.data);
        var key=this.props.location.data;
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/zhi/edit',{
          id:key,
          name:user,
          num:loginid
        })
        .then((res)=>{
          console.log(res.data.data);
         this.setState({
          editText:res.data.data
         })
        }).catch((error)=>{
          console.log(error)
        })
      }

      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const {editText} =this.state;
        return(
    <div className="addbuyhousehelp">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">楼盘资讯 </Breadcrumb.Item>
            <Breadcrumb.Item href="">编辑买房指南 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">问：</p>
            <Form.Item >
            {getFieldDecorator('title', {
              initialValue:editText.title
                })(
            <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} />,
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">答：</p>
            <Form.Item >
            {getFieldDecorator('content', {
                 initialValue:editText.content
                })(
            <TextArea rows={4}   autosize={{ minRows: 10, maxRows: 13 }}  className="answer"/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          上传
          </Button>
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou