import React ,{Component} from 'react';
import {Form, Input, DatePicker, Button ,Icon,Select,Upload, Modal}  from 'antd'
import axios from 'axios'
let id = 0;
const { TextArea } = Input;
const { Option }= Select;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value, dateString) {
  console.log('Selected Time: ', value);
  console.log('Formatted Selected Time: ', dateString);
}



function onOk(value) {
  console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
        this.state={
          selectvalue:[],
          previewVisible: false,
          previewImage: '',
          data:[],
        }
      }
      componentDidMount(){
          this.add();
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
        axios.post('/index.php/pro/list',{
          name:user,
          num:loginid
        })
        .then((res)=>{
           console.log(res)
           if(res.data.code==200){
              console.log(res.data.name)
              this.setState({
                selectvalue:res.data.name
              })
           }
        }).catch((error)=>{
            console.log(error)
        })
      }
      }
     





      add = () => {
        const { form } = this.props;
        // can use data-binding to get
        const keys = form.getFieldValue('keys');
        const nextKeys = keys.concat(id++);
        // can use data-binding to set
        // important! notify form to detect changes
        form.setFieldsValue({
          keys: nextKeys,
        });
      };
      remove = k => {
        const { form } = this.props;
        // can use data-binding to get
        const keys = form.getFieldValue('keys');
        console.log(keys);
        // We need at least one passenger
        if (keys.length === 1) {
          return;
        }
    
        // can use data-binding to set
        form.setFieldsValue({
          keys: keys.filter(key => key !== k),
        });
      };


//搜索项目功能
handleSearch = value => {
  console.log(value)
  if (value) {
      if(loginid!='' && user!=""){
        const  loginid=localStorage.getItem('loginid');
        const user=localStorage.getItem('name');
      axios.post('/index.php/user/lsou',{
        num:loginid,
        name:user,
        n:value
      })
      .then((res)=>{
        console.log(res.data.data);
        var datalist=res.data.data;
        var data=[];
        datalist.map((m,n)=>{
            data.push({
               value:m.id,
               text:m.building_name
            })
            console.log(data);
        })
        this.setState({
          data :data
        })
      
      
      }).catch((error)=>{
      console.log(error)
      })
    }



  } else {
    this.setState({ data: [] });
  }

};
handleChange = value => {
  this.setState({ value });
};


      // onMenuClick(dailist){
      //   this.props.onMenuClick(dailist)
      // }
      render(){
        const options = this.state.data.map(d => <Option key={d.value}>{d.text}</Option>);
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout={
            labelCol:{
              span:4
            },
            wrapperCol:{
               span:20
              }
        }
        getFieldDecorator('keys', { initialValue: [] });
        const keys = getFieldValue('keys');
        const formItemLayoutWithOutLabel = {
          wrapperCol: {
            xs: { span: 24, offset: 0 },
            sm: { span: 20, offset: 4 },
          },
        };
        const formItems = keys.map((k, index) => (
          <Form.Item style={{ marginLeft:79}}
            required={false}
            key={k}
          >
            {getFieldDecorator(`project[${k}]`, {
              validateTrigger: ['onChange', 'onBlur'],
              rules: [
                {
                  required: false,
                  whitespace: true,
                  message: "请选择带看项目",
                },
              ],
            })(<Select placeholder="带看项目" style={{ width: '283px', marginRight: 8 }} 
                showSearch
                defaultActiveFirstOption={false}
                showArrow={false}
                filterOption={false}
                notFoundContent={null}
                onSearch={this.handleSearch}
                onChange={this.handleChange}
               >
                   {/* {
                       this.state.selectvalue.map((v,t)=>{
                              return(
                                <Option value={v.id} key={t}>{v.building_name}</Option>
                              )
                       }) 
                   } */}
                   {options}


              </Select>)}
            {keys.length > 1 ? (
              <Icon
                className="dynamic-delete-button"
                type="minus-circle-o"
                onClick={() => this.remove(k)}
              />
            ) : null}
          </Form.Item>
        ));
        
        return( 
          <div className="news">
               <Form > 
               <Form.Item label="约看时间" {...formItemLayout}>
          {getFieldDecorator('time', {
            rules: [{ required: false, message: '请选择约看时间!' }],
          })(
            
            <DatePicker showTime placeholder="请选择约看时间" onChange={onChange} onOk={onOk} style={{ width: '283px', marginRight: 8 }}/>
          )}
        </Form.Item>
          <Form.Item >
            <span style={{ color:"#000",marginLeft:10}}>带看项目:</span>
              {formItems}
          <Button type="dashed" onClick={this.add} style={{ width: '283px',marginLeft:79 }}>
            <Icon type="plus" /> 添加
          </Button>
      
        </Form.Item>
       
        <Form.Item  label="约看备注" {...formItemLayout}>
          {getFieldDecorator('bei', {
            rules: [{ required: false, message: '请填写约看备注!' }],
          })(
            <TextArea rows={4}  placeholder="请填写约看备注"/>
          )}
        </Form.Item>
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms