import React ,{Component} from 'react';
import { Breadcrumb,Row,Form,Input,Button,Col,message} from 'antd'
import '../../style/personInformation/baseInformation.scss'
import axios from 'axios'
import { Link } from 'react-router-dom'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class InformationForm extends Component{
      constructor(props){
        super(props)
        this.state={
              info:[],
        }
      }
      getInformation=()=>{
        // var id=localStorage.getItem('login');
        // console.log(id);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/staff/my',{
            name:user,
            num:loginid,
          })
          .then((res)=>{
            console.log(res);
            this.setState({
                 info:res.data.data
            })
          
       }).catch((error)=>{
           console.log(error);
       })
      }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('提交内容为: ', values);
         
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              console.log(loginid,user);
              if(loginid!='' && user!=""){
              axios.post('/index.php/staff/change',{
                  value:values,
                   name:user,
                   num:loginid,
              })
              .then((res)=>{
                    if(res.data.code==200){
                      console.log(res);
                       message.success('修改密码成功')
                    }else if(res.data.code==501){
                      message.error('原始密码错误')
                    }
              }).catch((error)=>{
                console.log(error)
              })
            }




          }
        });
      };
      componentDidMount(){
            this.getInformation();
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { info } =this.state;
        return(
          <div className="personInformation">
              <Breadcrumb>
                  <Breadcrumb.Item ><Link to="/personInformation/baseInformation"> 个人信息</Link></Breadcrumb.Item>
                  <Breadcrumb.Item><Link to="/personInformation/baseInformation">基本信息</Link> </Breadcrumb.Item>
                </Breadcrumb>
            <div className="base">
                <h3 className="title">基本信息</h3>
                <br style={{color:'rgba(237,237,237,1)'}}/>
               
                  <ul className="ulbase">
                    <li><span>姓名：</span>{info.name}</li>
                    <li><span>性别：</span>{info.sex}</li>
                    <li><span>身份证号：</span>{info.card_id}</li>
                    <li><span>学历：</span>{info.education}</li>

                    <li><span>专业：</span>{info.profession}</li>
                     <li><span>城市：</span>{info.city}</li>
                     <li><span>部门：渠道</span>{info.department}</li>
                     <li><span>岗位：</span>{info.job}</li>

                     <li><span>入职时间：</span>{ info.r_time}</li>
                     <li><span>居住地址：</span>{ info.address}</li>
                     <li><span>生日：</span>{info.birthday}</li>
                     <li><span>电话：</span>{info.urgent }</li>
                   
                  </ul>
            </div>
            <div className="family">
                <h3 className="title">家庭信息</h3>
                
                <p><span>户籍地址：</span>{info.huji}</p>
                <h3 className="ptitle">家庭成员</h3>
                  <ul className="ulfamily">
                    <li><span>姓名：</span>{info.name1}</li>
                    <li><span>电话：</span>{info.tel1}</li>
                    <li><span>出身年月：</span>{info.birth1}</li>
                    <li><span>姓名：</span>{info.name2}</li>
                     <li><span>电话：</span>{info.tel2}</li>
                     <li><span>出身年月：</span>{info.birth2}</li>
                  </ul>
            </div>
            {/* <div className="box"> */}
            <br/>
           <h5 className="tit">修改密码</h5>
           <br/>
           <br/>
           <br/>
           <br/>
            <Form  layout="inline"  onSubmit={this.handleSubmit} >
            <Row gutter={24}>
              <Col  span={8}>
            <Form.Item label="原密码" >
            {getFieldDecorator('old', {
              //  initialValue:newsdetail.title
                })(
               <Input placeholder="请输入" style={{ width:200}}/>
                )}
            </Form.Item >
            </Col>
            <Col span={8}>
            <Form.Item label="修改密码" >
            {getFieldDecorator('new', {
              //  initialValue:newsdetail.title
              rules: [
              {pattern:/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{0,8}$/,message:'请输入8位数字加字母'}
                ],
                })(
               <Input placeholder="请输入" style={{ width:200}}/>
                )}
            </Form.Item >
            <p>注：密码为8位字母数字组合</p>
            </Col>
        

            <Form.Item  >
                <Button type="primary" htmlType="submit" className="sub">
                  保存
              </Button>
            </Form.Item >
            </Row>
            </Form>
            {/* </div> */}
           
           
          </div>
          )
      }
}
const personInformation = Form.create()(InformationForm);
export default personInformation