import React ,{Component} from 'react';
import{Breadcrumb,Tabs,DatePicker, LocaleProvider} from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";
import DataSet from "@antv/data-set";
import axios from 'axios'
import  '../../../style/statistics/telfollow.scss'
import $ from 'jquery'
import { Link } from 'react-router-dom'
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
const { DataView } = DataSet;
const { Html } = Guide;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class telFollow extends Component{
      constructor(props){
        super(props)
        this.state={
          data:[],
          dvv:[],
          colss:[],

          si:[],
          gong:[],
          zong:'',
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        var num=1;
        if(loginid!='' && user!=""){
          axios.post('/index.php/gen/diangen',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.data);
            this.setState({
              data:res.data.data,
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      selectDate=(e)=>{
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        $(e.target).addClass("current").siblings().removeClass("current")
        var num=e.target.getAttribute('data-key');
        localStorage.setItem('timetype',num);
        // console.log(num);
        if(loginid!='' && user!=""){
          axios.post('/index.php/gen/diangen',{
             name:user,
             num:loginid,
             type:num
          })
          .then((res)=>{
            console.log(res.data.data);
            var num1=res.data.data;
            console.log(res.data.gids);
            console.log(res.data.sids);
            var  zongdata=0;
            if(num1.length>0){
              num1.map((m,n)=>{
                zongdata+=m.count;
                console.log(zongdata);
              })
            }
          
            this.setState({
              data:res.data.data,
               zong:zongdata
            })
            const dv= new DataView();
            dv.source(this.state.data).transform({
              type: "percent",
              field: "count",
              dimension: "item",
              as: "percent"
            });
            const cols = {
              percent: {
                formatter: val => {
                  val = val * 100 + "%";
                  return val;
                }
              }
            };
            this.setState({
               dvv:dv,
               colss:cols
            })
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      godetail=(e)=>{
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        var key=e.target.getAttribute('data-key');
        var type=localStorage.getItem('timetype');
        console.log(type);
        console.log(key);
        if(key==1){
          if(loginid!='' && user!=""){
            axios.post('/index.php/gen/diangen',{
               name:user,
               num:loginid,
               type:type
            })
            .then((res)=>{
              this.setState({
                  si:res.data.sids
              })
              this.props.history.push({ pathname:'/statistics/telfollowdetail',data:this.state.si})
              console.log(res);
              console.log(res.data.gids);
              console.log(res.data.sids);
            }).catch((error)=>{
              console.log(error)
            })
          }
        }else if(key==2){
          if(loginid!='' && user!=""){
            axios.post('/index.php/gen/diangen',{
               name:user,
               num:loginid,
               type:type
            })
            .then((res)=>{
              this.setState({
                gong:res.data.gids
               })
            this.props.history.push({ pathname:'/statistics/telfollowdetail',data:this.state.gong})
            }).catch((error)=>{
              console.log(error)
            })
          }
        }
       
      }
      render(){
        const { zong } =this.state;
        return(
          <LocaleProvider locale={zhCN}>
          <div className="telfollow">
              <Breadcrumb>
              <Breadcrumb.Item  ><Link to="/statistics/telfollow"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item > <Link to="/statistics/telfollow">电话跟进 </Link></Breadcrumb.Item>
          </Breadcrumb>
              <div className="option">
               
                    <ul className="time" style={{ cursor:'pointer'}}>
                    <li> <p style={{ marginRight:'650px',fontSize:'20px',fontWeight:400,color:'rgba(52,52,52,1)'}}>电话跟进</p></li>
                        <li onClick={this.selectDate.bind(this)} data-key={'1'}>日</li>
                        <li onClick={this.selectDate.bind(this)} data-key={'2'}>周</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'3'}>月</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'4'}>年度</li>
                    </ul>
                    <RangePicker onChange={onChangeDate} style={{width:400,marginLeft:80}} />
        </div>
        <Chart
    height={500}
    width={ 1025 }
    data={this.state.dvv}
    scale={this.state.colss}
    padding={[15, 0, 0, -200]}
    forceFit={false}
    style={{ float:'left'}}
      >
    <Coord type={"theta"} radius={0.75} innerRadius={0.7} />
    <Axis name="percent" />
    <Legend
    position="right-top"
    offsetX={-200}
    offsetY={100}
    textStyle={{  fontSize: '14',}}
    />
    <Tooltip
    showTitle={false}
    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
    />
    <Guide>
    <Html
      position={["50%", "50%"]}
      // html="<div style=&quot;color:#8c8c8c;font-size:1.16em;text-align: center;width:4rem;&quot;>总电话量<br><span style=&quot;color:#262626;font-size:1.5em&quot;>67</span>人</div>"
      html={(xScale, yScale) => {
        console.log(yScale);
        return `<div>总电话量${zong}个</div>`; 
      }}
      alignX="middle"
      alignY="middle"
    />
    </Guide>
    <Geom
    type="intervalStack"
    position="percent"
    color="item"
    tooltip={[
      "item*percent",
      (item, percent) => {
        percent = percent * 100 + "%";
        return {
          name: item,
          value: percent
        };
      }
    ]}
    style={{
      lineWidth: 3,
      stroke: "#fff"
    }}
    >

    </Geom>
    </Chart>
     <div className="detail">
       <span onClick={this.godetail.bind(this)} data-key={'1'}>私客量详情</span><br/>
       <span onClick={this.godetail.bind(this)}  data-key={'2'}>公客量详情</span>
     </div>
          </div>
        </LocaleProvider>
          )
      }
}
  export default telFollow