import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,

} from 'antd';
import axios from 'axios'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class RegistForm extends Component{
      constructor(props){
        super(props)
        
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
            axios.post('/index.php/role/save',{
              value:values,
              name:user,
              num:loginid
            })
            .then((res)=>{
              
              if(res.data.code==200){
                console.log('成功');
                this.props.history.push('/personmanage/positionpower');
              }else {
               
              }
            }).catch(error=>{
              console.log(error)
            })
          }
          }
        });
      };
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
    <div className="addtuangou">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 人员管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 岗位权限设置 </Breadcrumb.Item>
            <Breadcrumb.Item href="">新增岗位</Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">添加岗位</p>
            <br/>
            <Form.Item >
            {getFieldDecorator('name', {
                })(
            <Input rows={4}   style={{ width:1000}} placeholder="请输入"/>
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">岗位描述</p>
            <br/>
            <Form.Item >
            {getFieldDecorator('introduce', {
                })(
            <TextArea rows={4}   autosize={{ minRows: 6, maxRows:8 }} placeholder="请输入"/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          提交
          </Button>
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou