import React ,{Component} from 'react';
import {Breadcrumb,Form,Button,Select,Input,Table,Menu, Dropdown,Icon,LocaleProvider,Divider,Modal ,message} from 'antd'
import '../../../style/loupannews.scss'
import axios from 'axios'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import {Link} from 'react-router-dom'
import '../../../style/buyhousehelp.scss'
const {Option}=Select
const { confirm } = Modal;
function handleMenuClick(e) {
  message.info(e);
  console.log('click', e.key);
  sessionStorage.setItem('wentype',e.key);
 var key= sessionStorage.getItem('wentype');
  console.log(key);
}
const menu = (
  <Menu onClick={handleMenuClick}>
    <Menu.Item key="买房">
      <Link to="/projectmanage/projectInformation/addbuyhousehelp">买房</Link>
    </Menu.Item>
    <Menu.Item key="投资">
     <Link to="/projectmanage/projectInformation/addbuyhousehelp">投资 </Link>
    </Menu.Item>
    <Menu.Item key="贷款">
     <Link to="/projectmanage/projectInformation/addbuyhousehelp">贷款 </Link>
    </Menu.Item>
  </Menu>
);
function showConfirm() {
  confirm({
    title: '确定删除该问答吗?',
    content: '删除问答后不可恢复',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[]
        }
      }
    
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            axios.post('/index.php/zhi/sou',{
              title:values.title,
              name:user,
              num:loginid
            })
            .then((res)=>{
              console.log(res.data.data);
              if(parseInt(res.status) == 200){
                var number=res.data.data
                var   list=[];
                number.map((v,j)=>{
              list.push({
                        key:v.id,
                        quest:v.title,
                        type:v.type,
                        answer:v.content,
                        uploadtime:v.create_time,
                    })
                    this.setState({
                     datanum:list,
                    })
                })
             }else{
                console.log("请求数据失败")
             }




             
            }).catch((error)=>{
              console.log(error)
            })
          }

          }
        });
      };
      editZhiNan(num){
        console.log(num);
          this.props.history.push({pathname:'/projectmanage/projectInformation/editbuyhousehelp',data:num});
      }
      deleteZhiNan(num){
        console.log(num);
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        console.log(loginid,user);
        if(loginid!='' && user!=""){
        axios.post('/index.php/zhi/delete',{
          id:num,
          name:user,
          num:loginid
        }).then((res)=>{
              console.log(res)
        }).catch((error)=>{
          console.log(error)
        })
      }
      }
      componentDidMount(){
           //项目列表 
        // dais: []
        // mais: []
        // tus: []
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        console.log(loginid,user);
        if(loginid!='' && user!=""){
        axios.post('/index.php/zhi/index',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          console.log(res);
          if(parseInt(res.status) == 200){
             var number=res.data.mais
             console.log(number);
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     quest:v.title,
                     type:v.type,
                     answer:v.content,
                     uploadtime:v.create_time,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }

      }
      handleSelectChange=(value)=>{
           console.log(value);
           var loginid=localStorage.getItem('loginid');
           var user=localStorage.getItem('name');
           console.log(loginid,user);
           if(value=='买房'){
            if(loginid!='' && user!=""){
            axios.post('/index.php/zhi/index',{
              name:user,
              num:loginid
            })
            .then((res)=>{
              console.log(res);
              if(parseInt(res.status) == 200){
                 var number=res.data.mais
                 console.log(number);
                 number.map((v,j)=>{
                 this.state.datanum.push({
                         key:v.id,
                         quest:v.title,
                         type:v.type,
                         answer:v.content,
                         uploadtime:v.create_time,
                     })
                 var  datanum=this.state.datanum
                     this.setState({
                      datanum:datanum,
                      loading:false
                     })
                 })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
          }
           }else if(value=='投资'){
            if(loginid!='' && user!=""){
            axios.post('/index.php/zhi/index',{
              name:user,
              num:loginid
            })
            .then((res)=>{
              console.log(res.data.tus);
              if(parseInt(res.status) == 200){
                 var number=res.data.tus
                var array=[];
                 number.map((v,j)=>{
                array.push({
                         key:v.id,
                         quest:v.title,
                         type:v.type,
                         answer:v.content,
                         uploadtime:v.create_time,
                     })
                     this.setState({
                      datanum:array,
                     })
                 })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
           } }else if(value=='贷款'){
            if(loginid!='' && user!=""){
            axios.post('/index.php/zhi/index',{
              name:user,
              num:loginid
            })
            .then((res)=>{
             
              if(parseInt(res.status) == 200){
                console.log(res.data.dais);
                 var number=res.data.dais;
                 var arr=[];
                 number.map((v,j)=>{
                arr.push({
                         key:v.id,
                         quest:v.title,
                         type:v.type,
                         answer:v.content,
                         uploadtime:v.create_time,
                     })
                     this.setState({
                      datanum:arr,
                     })
                 })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
           }
          }
      }
      render(){
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper:{goButton:<Button type="primary">确定</Button>},
          showTotal: ((total,count) => {
            console.log(count);
             return `共 ${total} 条`;
          }),
          // showTotal: () => `共${totals}条`,
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
          // onChange: (current) => this.changePage(current),
        };
        const columns = [
          {
            title: '问',
            dataIndex: 'quest',
           
          },
          {
            title: '类别',
            dataIndex: 'type',
          },
          {
            title: '答',
            dataIndex: 'answer',
          },
          {
            title: '上传时间',
            dataIndex: 'uploadtime',
          },
          {
            title: '操作',
            dataIndex: 'handel',
            render:(text, record) => (
              <span>
                <a href="javascript:;" onClick={this.editZhiNan.bind(this,record.key)}>编辑</a>
                <Divider type="vertical" />
                <a href="javascript:;" onClick={this.deleteZhiNan.bind(this,record.key)}>删除</a>
              </span>
            )
          },

        ];
        const { getFieldDecorator } = this.props.form;
        const rowSelection = {
          onChange: (selectedRowKeys, selectedRows) => {
            console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          },
          getCheckboxProps: record => ({
            disabled: record.name === 'Disabled User', // Column configuration not to be checked
            name: record.name,
          }),
        };
        return(
          <LocaleProvider locale={zhCN}>
          <div className="buyhousehelp">
               <Breadcrumb>
                    <Breadcrumb.Item ><Link to="/projectmanage/projectInformation/projectlist"> 项目管理</Link></Breadcrumb.Item>
                    <Breadcrumb.Item > <Link to="/projectmanage/projectInformation/projectlist"> 项目信息 </Link></Breadcrumb.Item>
                    <Breadcrumb.Item > <Link to="/projectmanage/projectInformation/buyhousehelp"> 买房指南</Link> </Breadcrumb.Item>
                </Breadcrumb>
                <div className="tabledata">
                    <div className="search">
                    <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
                    <Form.Item label="类别">
                      {getFieldDecorator('type', {
                        rules: [{ required: false, message: '请选择!' }],
                      })(
                        <Select
                        style={{ width:'360px',marginRight:'65px'}}
                          placeholder="买房"
                          onChange={this.handleSelectChange}
                        >
                          <Option value="买房">买房</Option>
                          <Option value="投资">投资</Option>
                          <Option value="贷款">贷款</Option>
                        </Select>,
                      )}
                    </Form.Item>  
                      
                    <Form.Item label="问">
                      {getFieldDecorator('title', {
                        rules: [{ required: false, message: '请输入!' }],
                      })(
                        <Input   style={{ width:'360px',marginRight:'60px'}}/>
                      )}
                      </Form.Item>
                      <Form.Item>
                        <Button type="primary" htmlType="submit" style={{ width:'88px',marginRight:'210px'}}>查询</Button>
                      </Form.Item>

                      <Form.Item>
                        <Link to="/projectmanage/projectInformation/addbuyhousehelp" >新增指南</Link>
                      </Form.Item>
                      <Form.Item>
                      <Dropdown overlay={menu} >
                            <Button style={{ width:'150px'}}>
                              买房 <Icon type="down" />
                            </Button>
                          </Dropdown>
                      </Form.Item>
                    </Form>
                    </div>
                    <div className="table">
                         <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading}/>
                         {/* <p className="yema">共&nbsp;<span>10&nbsp;</span>&nbsp;条   第&nbsp;<i>1/50&nbsp;</i>页&nbsp;</p> */}
                         <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}} onClick={showConfirm}>删除</Button>
                    </div>
                </div>
          </div>
          </LocaleProvider>
          )
      }
}

const BuyHouseHelp = Form.create()(RegistForm);
  export default BuyHouseHelp