validFunction = (rule, val, callback) => {
  // console.log(val);
  if (!val) {
  callback();
  }
  let validateResult =/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57]|19[0-9]|16[0-9])[0-9]{8}$/ ; 
  let wei=/\b\d{12}\b/
  let other= /^[1]((3[0-9])|(4[5,7,9])|(5[4])|(8[0-9])|(7[0,1,3,5,6,7,8]))\d{8}$/;
  var result=(!validateResult.test(val));
  var res=!wei.test(val);
  var ress=!other.test(val)
  console.log(result, res);
  if(result && res && ress){
  callback('请输入正确手机号或座机号！'); 
  }
  callback();
  }




