import React ,{ Component} from 'react';
import { Form, Input, Icon, Button , Upload,Modal} from 'antd';
function getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  
  
  
let id = 0;

class TextForm extends  Component {
    constructor(props){
          super(props)
          this.state={
            previewVisibleHu: false,
            previewImageHu: '',
            fileListHu: [],
          }
    }
    
    handleCancelHu = () => this.setState({ previewVisibleHu: false });

    handlePreviewHu = async file => {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj);
      }
  
      this.setState({
        previewImageHu: file.url || file.preview,
        previewVisibleHu: true,
      });
    };
  
    handleChangeHu = ({ fileList }) => this.setState({ fileListHu : fileList });
  remove = k => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    // We need at least one passenger
    if (keys.length === 1) {
      return;
    }

    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter(key => key !== k),
    });
  };

  add = () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(id++);
    form.setFieldsValue({
      keys: nextKeys,
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { keys, names } = values;
        console.log('提交的数据为 ', values);
        console.log('Merged values:', keys.map(key => names[key]));
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { previewVisibleHu, previewImageHu, fileListHu } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    getFieldDecorator('keys', { initialValue: [] });
    const keys = getFieldValue('keys');
    const formItems = keys.map((k, index) => (
       
        <div style={{border:'1px solid #ff0',width:322,float:'left',marginRight:'30px'}}>
      <Form.Item>
      <Upload
          action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
          listType="picture-card"
          fileList={fileListHu}
          onPreview={this.handlePreviewHu}
          onChange={this.handleChangeHu}
        >
          {fileListHu.length >= 1 ? null : uploadButton}
        </Upload>
        <Modal visible={previewVisibleHu} footer={null} onCancel={this.handleCancelHu}>
          <img alt="example" style={{ width: '100%' }} src={previewImageHu} />
        </Modal>
     </Form.Item>
     <Form.Item label="室厅">
     {getFieldDecorator(`ting[${k}]`, {
         
        })(<Input placeholder="室厅" style={{ width: '250px', marginRight: 8 }} />)}
     </Form.Item>
     <Form.Item label="面积">
     {getFieldDecorator(`area[${k}]`, {
        })(<Input placeholder="室厅" style={{ width: '250px', marginRight: 8 }} />)}
     </Form.Item>
     <Form.Item label="售价">
     {getFieldDecorator(`price[${k}]`, {
        })(<Input placeholder="售价" style={{ width: '250px', marginRight: 8 }} />)}
     </Form.Item>
     {keys.length > 1 ? (
          <Icon
            className="dynamic-delete-button"
            type="minus-circle-o"
            onClick={() => this.remove(k)}
          />
        ) : null}
      </div>
    ));
    return (
      <Form onSubmit={this.handleSubmit} layout="inline">
        {formItems}
        <Form.Item>
          <Button  onClick={this.add} style={{ width:120}}>
            <Icon type="plus" /> 添加文件
          </Button>
        </Form.Item>
        <Form.Item >
          <Button type="primary" htmlType="submit">
            提交
          </Button>
        </Form.Item>
      </Form>
    );
  }
}
const Text=Form.create()(TextForm);
export default Text;