import React ,{Component} from 'react';
import { Menu, Dropdown, Icon } from 'antd';
import {Link} from 'react-router-dom'
import axios from 'axios'
// const id=this.props.text

class dropdown extends Component{
      constructor(props){
        super(props)
        this.state={
              jia:'上架',
              currentIndex:'rgba(128,128,128,1)',

            item:''
        }
      }
      onVisibleChange=(visible)=>{
          console.log(this.props.text);
          sessionStorage.setItem('key',this.props.text);
          this.getJia();
      }
      componentDidMount(){
          
      }
      getJia=()=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var id= sessionStorage.getItem('key')
        if(loginid!='' && user!=""){
          axios.post('/index.php/pro/edit',{
            id:id,
            name:user,
            num:loginid
            })
           .then((res)=>{
              if(res.data.code==200){
                 console.log(res.data.dd.isdeng);
                 this.setState({
                  item:res.data.dd.isdeng
                 })
                 const { item } =this.state;
                 if(item==null||item=='否'){
                  this.setState({
                    currentIndex:'rgba(24,144,255,1)',
                    jia:'下架'
                  })
                }else if(item=='是'){
                  this.setState({
                    currentIndex:'rgba(128,128,128,1)',
                    jia:'上架'
                  })
                }

             }else {
               
             }
           
           }).catch((err)=>{
             console.log(err)
           })
          }
      }
      bian=()=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        const { item,jia} =this.state;
        var id= sessionStorage.getItem('key')
         if(jia=='上架'){
          if(loginid!='' && user!=""){
            axios.post('/index.php/pro/isdeng',{
              id:id,
              type:1,
              name:user,
              num:loginid
              })
             .then((res)=>{
                if(res.data.code==200){
                   console.log(res);
                   
               }else {
                 console.log('请求失败')
               }
             
             }).catch((err)=>{
               console.log(err)
             })
            }


              this.setState({
                    currentIndex:'rgba(24,144,255,1)',
                    jia:'下架'
                  })
         }else if(jia=='下架'){
          if(loginid!='' && user!=""){
            axios.post('/index.php/pro/isdeng',{
              id:id,
              type:2,
              name:user,
              num:loginid
              })
             .then((res)=>{
                if(res.data.code==200){
                   console.log(res);
                   
               }else {
                 console.log('请求失败')
               }
             
             }).catch((err)=>{
               console.log(err)
             })
            }
          this.setState({
            currentIndex:'rgba(128,128,128,1)',
            jia:'上架'
          })
         }
      
        
      }
      render(){
        const { jia ,currentIndex}= this.state;
        const menu = (
          <Menu>
            <Menu.Item >
              {/* <span onClick={tiaoEdit}>编辑</span> */}
               <Link to="/projectmanage/projectInformation/projectedit">
                编辑
              </Link>
            </Menu.Item>
            {/* <Menu.Item>
               <Link to="/projectmanage/projectInformation/photomanage" >
              相册管理
              </Link>
            </Menu.Item> */}
            <Menu.Item>
               <Link to="/projectmanage/projectInformation/photomanedit" >
              相册编辑
              </Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/projectmanage/projectInformation/Addtuangou">
                添加团购
              </Link>
            </Menu.Item>
            <Menu.Item>
                <Link to="/projectmanage/projectInformation/projectdongtaiadd">
                项目动态
                </Link>
            </Menu.Item>
            <Menu.Item>
                <Link to="/projectmanage/projectInformation/Addtuangou">
                预览
                </Link>
            </Menu.Item>
            {/* <Menu.Item>
                <Link to="/projectmanage/projectInformation/Addtuangou">
                删除
                </Link>
            </Menu.Item> */}
            <Menu.Item>
              <span onClick={this.bian.bind(this)} style={{color: currentIndex}}>
                {jia}
              </span>
            </Menu.Item>
            <Menu.Item>
                <Link to="/projectmanage/projectInformation/projectfen">
                项目分析
                </Link>
            </Menu.Item>
          </Menu>
        );
        return(
          <div className="dropdown">
          {/* { console.log(this)} */}
                    <Dropdown overlay={menu} onVisibleChange={this.onVisibleChange}>
                     <a className="ant-dropdown-link" href="#" style={{color:'rgba(24,144,255,1)'}}>
                       选择 <Icon type="down" />
                    </a>
              </Dropdown>
          </div>
          )
      }
}
  export default dropdown