import React ,{Component} from 'react';
import {Layout} from 'antd'
const {Content} =Layout;
class Con extends Component{
      constructor(props){
        super(props)
      }
      render(){
        return(
          <div className="Content">
                 <Content style={{ margin: '22px  30px 16px 20px',minHeight:'88.8vh',minWidth: '84.3vw',backgroundColor:'#fff',padding:'0 20px 0 0' }}>
                     {this.props.children}
                </Content>
          </div>
          )
      }
}
  export default Con