      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('搜索字段为: ', values);
            const  loginid=localStorage.getItem('loginid');
            const user=localStorage.getItem('name');
            if(loginid!='' && user!=""){
            axios.post('/index.php/user/sou',{
              value:values,
              num:loginid,
              name:user,
           })
           .then((res)=>{
             console.log(res);
            
             if(res.data.code===200){
              console.log('提交成功');
              
               var num=res.data.data;
               if(num.length>0){
               var arr=[];
               num.map((v,k)=>{
                     arr.push({
                      key:v.id,
                      tag:v.grade,
                      label:v.label,
                      re:v.re,
                      dai:v.dai,
                      fu:v.fu,

                      customername:v.name,
                      jkproject:v.project,
                      jkorigin:v.port,
                      rtime: v.time,
                     zjtime:v.g_time,
                      isnow:v.isnow,

                      txdate:v.grade,
                      rpeople:v.s_id,
                      wtpeople:v.sid,
                      tel:v.tel
                     })
               })
               this.setState({
                datanum:arr,
                loading:false,
                sou:true,
                value:values,
                total:res.data.total
               })
              }
              else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
             }
           }).catch((error=>{
              console.log(error);
           }))
          }

          }
        })
      }

