import React ,{Component} from 'react';
import {Breadcrumb} from 'antd'
import '../../../style/statistics/volumedetail.scss'
import axios from 'axios'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class volume extends Component{
      constructor(props){
        super(props)
        this.state={
            data:[],
            element:[],
            protocol:[],
            confirm:[],
            site:[],

        }
      }
      componentDidMount(){
           var key=this.props.location.data;
           console.log(key);
           var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
           if(loginid!='' && user!=""){
            axios.post('/index.php/recording/edit',{
              name:user,
              num:loginid,
              id:key
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  this.setState({
                    data:res.data.data,
                    element:res.data.data.element,
                    protocol:res.data.data.protocol,
                    confirm:res.data.data.confirm,
                    site:res.data.data.site,
                  })
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
          }
      }
      render(){
        const { data, element, protocol, confirm, site } =this.state;
        return(
          <div className="volumedetail">
            <div className="bread">
            <Breadcrumb>
                <Breadcrumb.Item href="/"> 客户管理</Breadcrumb.Item>
                <Breadcrumb.Item href=""> 客户列表 </Breadcrumb.Item>
                <Breadcrumb.Item href="">添加客户 </Breadcrumb.Item>
            </Breadcrumb>
            </div>
            <div className="formbox">
          {/* 客户基本信息*/}
      <p className="title1 txt">基本信息</p>  
                <ul className="kehuxinxi">
                    <li>客户编号:<span>{data.id}</span></li>
                    <li>成交项目:<span>{data.project}</span></li>
                    <li>成交日期:<span>{data.time}</span></li>
                    <li>成交金额:<span>{data.money}</span></li>

                    <li>业绩:<span>{data.yeji}</span></li>
                    <li>客户姓名:<span>{data.name}</span></li>
                    <li>客户电话:<span>{data.tel}</span></li>
                    <li>成交房源:<span>{data.house}</span></li>

                    <li>付款方式：<span>{data.type}</span></li>
                    <li>预计签约时间：<span>{data.q_time}</span></li>
                    <li>预计付首付/全款时间：<span>{data.p_time}</span></li>
                </ul>
                <div class="imglist">
                <dl>
                  <dt>成交三要素</dt>
                  {
                      element.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      })
                  }
                 
                </dl>
                <dl>
                  <dt>返佣协议</dt>
                  {
                      protocol.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      })
                  }
                </dl>
                <dl>
                  <dt>返佣确认单</dt>
                  {
                      confirm.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      })
                  }
                </dl>
                <dl>
                  <dt>成交现场确认照片</dt>
                  {
                      site.map((m,n)=>{
                           return(
                            <dd> <img src={"http://"+m} alt=""/></dd>
                           )
                      })
                  }
                </dl>
                </div>
 

        </div>


          </div>
          )
      }
}
  export default volume