import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Menu, 
 Dropdown, 
 DatePicker } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
 import axios from 'axios' 
 import '../../style/sawcustomer.scss'
 function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function onChangeTime(date, dateString) {
  console.log(date, dateString);
}
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class sawcustomerForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          obj:[],
          dainum:[],
          cheng:[],
          qu:[],
          zu:[],
          peo:[]
        }
      }
      componentDidMount(){
       
        this.region();
        this.getCity();
        this.getProjectData();

      }
        //表单提交
        handleSubmit = e => {
          e.preventDefault();
          this.props.form.validateFields((err, values) => {
            if (!err) {
              console.log(err);
              console.log('输入的结果为', values);
              if(loginid!='' && user!=""){
                var loginid=localStorage.getItem('loginid');
                var user=localStorage.getItem('name');
              axios.post('/index.php/dai/dusersou',{
                value:values,
                name:user,
               num:loginid
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  var number=res.data.data;
                  var arr=[];
                  if(number.length>0){
                  number.map((v,j)=>{
                 arr.push({
                          key:v.id,
                          tag:v.grade,
                          label:v.label,
                          re:v.re,
                          dai:v.dai,
                          fu:v.fu,

                          customername:v.name,
                          dkproject:v.project,
                          jkorigin:v.port,
                          dktime:v.create_time,
                          dkpeople:v.s_id,
                      })
                      this.setState({
                       datanum:arr,
                       loading:false
                      })
                    })
                  }else{
                    this.setState({
                      datanum:'',
                      loading:false
                     })
                  }
                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
            }
              
            }else{
  
            }
  
  
          });
        }
    
        region(){
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/pro/getarea',{
            name:user,
          num:loginid
          })
          .then((res)=>{
            var arr=res.data.data
            const list = Object.keys(arr).map((item, index) => ({ value:arr[item],label: item}));
            console.log(list);
            list.map(ite => {
              const shi = Object.keys(ite.value).map((j, index) => ({ value:ite.value[j],label: j}));
               this.state.obj.push({
                'value':ite.label,
                'label':ite.label,
                'children': shi.map(item=>{
                  const qu= Object.keys(item.value).map((k, index) => ({ value:item.value[k],label: item.value[k]}));
                  const  xian={
                    'value':item.label,
                    'label':item.label,
                    'children':qu
                  };
                  return xian;
                   })
                  
              })
             
            })
            console.log(this.state.obj);
          }).catch((error)=>{
          console.log(error)
          })
        }
        }
        getCity(){
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/qu/one',{ 
            name:user,
            num:loginid
             })
             .then((res)=>{
               if(res.data.code==200){
                console.log(res.data.data);
                var city=res.data.data;
                if(city){
                    this.setState({
                      cheng:city
                    })
                }
               
               }else {
               
               }
             }).catch(error=>{
               console.log(error)
             })
            }
        }
      //项目数据
      getProjectData(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/dai/duser',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            console.log(res);
             var number=res.data.data;
             if(number.length>0){
             
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     tag:v.grade,
                     label:v.label,
                     re:v.re,
                     dai:v.dai,
                     fu:v.fu,
                     customername:v.name,
                     dkproject:v.project,
                     jkorigin:v.port,
                     dktime:v.d_time,
                     dkpeople:v.s_id,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
               
            }else{
              this.setState({
                datanum:'',
                loading:false
               })
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
      }
        getDaiKanData(){
         var id= sessionStorage.getItem('daikanid');
         console.log(id);
         if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/dai/index',{
               id:id,
               name:user,
              num:loginid
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
             
               var number=res.data.data
                 console.log(number);
                 var array=[];
               number.map((v,j)=>{
                array.push({
                key: v.id,
                dktime: v.time,
                dkhouse: v.project,
                gjcontent:v.bei,
                yepeople:v.s_id,
                rtype:v.type
                   })
             this.setState({
                 dainum:array
             })
                })
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }

        }
        tiaoDetail(num){
          this.props.history.push({pathname:'/customermanage/customerdetail',data:num});
  
        }
        handleSelectCity(value){
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/qu/two',{ 
            name:user,
            num:loginid,
            id:value
             })
             .then((res)=>{
               if(res.data.code==200){
                console.log(res.data.data);
                var num=res.data.data;
                if(num){
                    this.setState({
                      qu:num
                    })
                }
               
               }else {
               
               }
             }).catch(error=>{
               console.log(error)
             })
            }
      
        }
        handleSelectQu(value){
          console.log(value);
          if(loginid!='' && user!=""){
         var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
       axios.post('/index.php/qu/two',{ 
         name:user,
         num:loginid,
         id:value
          })
          .then((res)=>{
            if(res.data.code==200){
             console.log(res.data.data);
             var data=res.data.data;
             if(data){
                 this.setState({
                   zu:data
                 })
             }
            
            }else {
            
            }
          }).catch(error=>{
            console.log(error)
          })
         }
      }
      changeZu=(value)=>{
        console.log(value);
        if(loginid!='' && user!=""){
         var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
       axios.post('/index.php/qu/peo',{ 
         name:user,
         num:loginid,
         id:value
          })
          .then((res)=>{
            if(res.data.code==200){
             console.log(res.data.data);
             var number=res.data.data;
             if(number){
                 this.setState({
                   peo:number
                 })
             }
            
            }else {
            
            }
          }).catch(error=>{
            console.log(error)
          })
         }
   }
      render(){
        const { dainum,cheng,qu,zu,peo} =this.state;
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper: {goButton:<Button type="primary">确定</Button>},
          showTotal: ((total,count) => {
            console.log(count);
             return `共 ${total} 条`;
          }),
          // showTotal: () => `共${totals}条`,
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
          // onChange: (current) => this.changePage(current),
        };
        const columns = [
          {
            title: '',
            dataIndex: 'tag',
            render:(text,record) =>{
              var lei=record.tag
              //  var type=record.label;
              //  var re=record.re;
              //  var dai=record.dai;
                // console.log(record);
               var str=""
               if( lei =="A" || lei=="A类" ){
                  return(
                     <a className="iconfont icon-zhongdianbiaozhu " href="javascript:;" style={{color:'red'}}></a> 
                  )
               }
            }

          },
          {
            title: '',
            dataIndex: 'tag1',
            render:(text,record) =>{
              var type=record.label;
              if(type=="一客多看"){
                return(
                  <a className="iconfont icon-ai-connection" href="javascript:;" style={{color:'rgba(101,195,255,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '标签',
            dataIndex: 'tag2',
            render:(text,record) =>{
              var num=record.fu;
              if(num=="复看"){
                return(
                  <a className="iconfont icon-renshu" href="javascript:;" style={{color:'rgba(196,132,255,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '',
            dataIndex: 'tag3',
              render:(text,record) =>{
              var dai=record.dai;
              if(dai=="新客" || dai=="老客" ){
                return(
                  <a className="iconfont icon-wo" href="javascript:;" style={{color:'rgba(41,204,114,1)'}}></a> 
                )
             }
            }
          },
          {
            title: '',
            dataIndex: 'tag4',
            render:(text,record) =>{
              var re=record.re;
            if(re=="成交"){
              return(
                <a className="iconfont icon-chengjiao" href="javascript:;" style={{color:'rgba(255,191,52,1)'}}></a> 
              )
            }
          }
          },
          {
            title: '客户姓名',
            dataIndex: 'customername',
          },
          {
            title: '带看项目',
            dataIndex: 'dkproject',
          },
          {
            title: '进客来源',
            dataIndex: 'jkorigin',
          },
          {
            title: '带看日期',
            dataIndex: 'dktime',
          },
          {
            title: '带看人',
            dataIndex: 'dkpeople',
          },
          {
            title: '操作',
            dataIndex: 'handle',
            render:(text ,record) =><div> <span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.tiaoDetail.bind(this,record.key)}>详情</span>&nbsp;</div>,
          },

        ];
        const { getFieldDecorator } = this.props.form;
        const columns1 = [
          {
            title: '带看时间',
            dataIndex: 'dktime',
           
          },
          {
            title: '带看项目',
            dataIndex: 'dkhouse',
          },
          {
            title: '带看内容',
            dataIndex: 'gjcontent',
          },
          {
            title: '业务员',
            dataIndex: 'yepeople',
          },
          {
            title: '录入方式',
            dataIndex: 'rtype',
          },
        ];
       
      const rowSelection = {
        type:'radio',
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
            var key=selectedRowKeys[0];
            console.log(key);
            sessionStorage.setItem('daikanid',key);
            this.getDaiKanData();
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
        return(
          <LocaleProvider locale={zhCN}>
          <div className="sawcustomer">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item > <Link to="/customermanage/customerlist" >客户管理</Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/customermanage/sawcustomer">带看客户</Link> </Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="类别">
              {/* {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择类型!' }],
              })( */}
                <Select
                style={{ width:'310px'}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="公客">公客</Option>
                  <Option value="私客">私客</Option>
                </Select>
              {/* )} */}
            </Form.Item>  

           

            <Form.Item label="区域" layout="inline">
            {getFieldDecorator('region', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Cascader options={this.state.obj} onChange={onChange} placeholder="请选择"  style={{ width:'300px'}} />
              )}
            </Form.Item>
            
            <Form.Item label="时间" layout="inline">
            {getFieldDecorator('time', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <RangePicker onChange={onChangeTime} style={{width:"310px",marginRight:"20px"}} />
              )}
              </Form.Item>
              <Form.Item label="项目" layout="inline">
            {getFieldDecorator('building_name', {
                rules: [{ required: false, message: '请输入项目名称' }],
              })(
                <Input onChange={onChangeTime} style={{width:"310px",marginRight:"110px"}} />
              )}
              </Form.Item>

              <Form.Item label="城市" layout="inline">
            {getFieldDecorator('city', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                    <Select  style={{ width:310}} onChange={this.handleSelectCity.bind(this)}>
                    {
                      cheng.length>0?cheng.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      }) : ''
                  }
                </Select>
              )}
            </Form.Item>
            <Form.Item label="项目区域" layout="inline">
            {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select style={{ width:300,marginRight:20}} onSelect={this.handleSelectQu.bind(this)}>
                {
                  qu.length>0?qu.map((m,n)=>{
                        return(
                         <Option value={m.id} key={n}>{m.name}</Option>
                        )
                  }) :''
             } 
               </Select>
              )}
            </Form.Item>
            <Form.Item label="组别" layout="inline">
            {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select  style={{ width:300,}} onChange={this.changeZu.bind(this)}>
                {
                     zu.length>0 ? zu.map((m,n)=>{
                          return(
                            <Option value={m.id } key={n}>{m.name} </Option>
                          )
                     }) :''
                }
             
  
              </Select>
              )}
            </Form.Item>
            <Form.Item label="人员" >
                {getFieldDecorator('id', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <Select
                      placeholder="所有"
                      onChange={this.handleSelectChange}
                      style={{width:300}}
                    >
                      {
                          peo.length>0 ? peo.map((m,n)=>{
                            return(
                              <Option value={m.id } key={n}>{m.name} </Option>
                            )
                       }) :''
                      }
                    </Select> 
                  )}
              </Form.Item>
             

              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
            </div>

            <div className="project-tab">
            <Tabs defaultActiveKey="1" onChange={callback}>
            <TabPane tab="带看记录" key="1">
                    <Table  columns={columns1} dataSource={dainum} size="middle" className="genjintablle" />
            </TabPane>
           
          </Tabs>
            </div>

            

          </div>
          </LocaleProvider>
          )
      }
}
const sawCustomer = Form.create()(sawcustomerForm);
export default sawCustomer