getdetail=()=>{
  var _this=this;
  var key=sessionStorage.getItem('key')
  var loginid=localStorage.getItem('loginid');
  var user=localStorage.getItem('name');
  if(loginid!='' && user!=""){
    axios.post('/index.php/pro/editpic',{
       id:key,
       name:user,
       num:loginid
    })
    .then((res)=>{
    
      if(res.data.code==200){
        console.log(res.data);
        //效果图
        var xiaog=res.data.x;
        var arr=[];
        xiaog.map((m,n)=>{
          arr.push({
            uid:m.id,
            name:m.id,
            status: 'done',
            url:`http://${m.x_big}`,
          })
        })
        console.log(arr);
        //实景图
        var shiJ=res.data.s;
        var array=[];
        shiJ.map((m,n)=>{
          array.push({
            uid:m.id,
            name:m.id,
            status: 'done',
            url:`http://${m.s_big}`,
          })
        })

         //样板图
         var yangB=res.data.y;
         var yang=[];
         yangB.map((m,n)=>{
          yang.push({
             uid:m.id,
             name:m.id,
             status: 'done',
             url:`http://${m.y_big}`,
           })
         })

         //配套图
        var peiT=res.data.p;
        var pei=[];
        peiT.map((m,n)=>{
         pei.push({
            uid:m.id,
            name:m.id,
            status: 'done',
            url:`http://${m.p_big}`,
          })
        })

        //交通
        var jiaoT=res.data.j;
        var jiao=[];
        jiaoT.map((m,n)=>{
         jiao.push({
            uid:m.id,
            name:m.id,
            status: 'done',
            url:`http://${m.j_big}`,
          })
        })

        _this.setState({
             fileList:arr,
             fileList2:array,
             fileList3:yang,
             fileList4:pei,
             fileList5:jiao,
             detailHuxing:res.data.h
        })
        // this.props.history.push('/projectmanage/projectInformation/projectlist');
      }else {
      
      }
    }).catch((error)=>{
      console.log(error);
    })
  }
}


