import React ,{Component} from 'react';
import {Form, Input, DatePicker, Button ,Icon,Select}  from 'antd'
import axios from 'axios'
const { Option} = Select
let id = 0;
const { TextArea } = Input;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value, dateString) {
  console.log('Selected Time: ', value);
  console.log('Formatted Selected Time: ', dateString);
}

function onOk(value) {
  console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
        this.state={
          yuetime:'',
          daiproject:[],
          yuebei:'',
          selectvalue:[]
        }
      }
      componentWillReceiveProps(nextProps){
          console.log(nextProps);
          var array=[]
         var array=nextProps.daiproject;
        //  console.log(array);
         const {daiproject}=this.state
         var  list = Object.keys(array).map((item, index) => ({ key:item,value: array[item]}));
          // console.log(array);
          this.setState({
            yuetime:nextProps.yuetime,
            yuebei:nextProps.yuebei,
             daiproject:list
       })
        // console.log(daiproject);
      }

      componentDidMount(){
          // this.add();
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
          axios.post('/index.php/pro/list',{
            name:user,
            num:loginid
          })
          .then((res)=>{
             console.log(res)
             if(res.data.code==200){
                console.log(res.data.name)
                this.setState({
                  selectvalue:res.data.name
                })
             }
          }).catch((error)=>{
              console.log(error)
          })
        }
      }
      add = () => {
        const { form } = this.props;
      
        // can use data-binding to get
        const keys = form.getFieldValue('keys');
        console.log(keys,keys[0].key);
            console.log(form.getFieldsValue());
            var keyid=keys[0].key++;
        // const nextKeys = keys.concat({key:keys[0].key++,value:keys.value});
        const nextKeys = keys.concat({key:keyid,value:keyid.value});
        // console.log(keys,keys[0].key);

        console.log(nextKeys);
        form.setFieldsValue({
          keys: nextKeys,
        });
      };
      remove = k => {
        const { form } = this.props;
        // can use data-binding to get
        const keys = form.getFieldValue('keys');
        // We need at least one passenger
        if (keys.length === 1) {
          return;
        }
    
        // can use data-binding to set
        form.setFieldsValue({
          keys: keys.filter(key => key !== k),
        });
      };
      ChangeProject=(value)=>{
          console.log(value);

      }
      render(){
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout={
            labelCol:{
              span:4
            },
            wrapperCol:{
               span:20
              }
        }
     
        getFieldDecorator('keys', { 
          initialValue: this.state.daiproject
        });
        const keys = getFieldValue('keys');
          console.log(keys);

        const formItems = keys.map((k, index) => (
        
          <Form.Item style={{ marginLeft:79}}
            required={false}
            key={k.key}
          >
            {getFieldDecorator(`project[${k.key}]`, {
               initialValue: k.value,
              validateTrigger: ['onChange', 'onBlur'],
              rules: [
                {
                  required: true,
                  whitespace: true,
                  message: "请输入带看项目",
                },
              ],
            })(<Select placeholder="带看项目" style={{ width: '283px', marginRight: 8 }} onChange={this.ChangeProject.bind(this)}>
            {
                this.state.selectvalue.map((v,t)=>{
                       return(
                         <Option value={v.id} key={t}>{v.building_name}</Option>
                       )
                }) 
            }
            
       </Select>
           )}
            {keys.length > 1 ? (
              <Icon
                className="dynamic-delete-button"
                type="minus-circle-o"
                onClick={() => this.remove(k)}
              />
            ) : null}
          </Form.Item>
        ));
        
        return(
          <div className="news">
               <Form > 
               <Form.Item label="约看时间" {...formItemLayout}>
          {getFieldDecorator('time', {
            rules: [{ required: false, message: '请选择约看时间!' }],
             initialValue: this.state.yuetime
          })(
            <Input style={{ width: '283px', marginRight: 8 }}/>
            // <DatePicker showTime placeholder="请选择约看时间" onChange={onChange} onOk={onOk} style={{ width: '283px', marginRight: 8 }}/>
          )}
        </Form.Item>
          <Form.Item >
            <span style={{ color:"#000",marginLeft:10}}>带看项目:</span>


              {formItems}


          <Button type="dashed" onClick={this.add} style={{ width: '283px',marginLeft:79 }}>
            <Icon type="plus" /> 添加
          </Button>
      
        </Form.Item>
        <Form.Item  label="约看备注" {...formItemLayout}>
          {getFieldDecorator('bei', {
            rules: [{ required: false, message: '请填写约看备注!' }],
             initialValue: this.state.yuebei
          })(
            <TextArea rows={4}  placeholder="请填写约看备注"/>
          )}
        </Form.Item>
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms