import React ,{Component} from 'react';
import {Form, Input, DatePicker ,Row,Col , Select}  from 'antd'
const {Option } =Select
const { TextArea } = Input;
function onChange(value, dateString) {
  // console.log('Selected Time: ', value);
  // console.log('Formatted Selected Time: ', dateString);
}

function onOk(value) {
  // console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
      }
   
      render(){
        const { getFieldDecorator } = this.props.form;
        return( 
          <div className="news">
               <Form layout="inline"> 
           <Row  gutter={24}>
             <Col span={12}>
                    <Form.Item label="标签" >
                {getFieldDecorator('tag', {
                    rules: [{ required: false, message: '请选择跟进时间!' }],
                })(
                    <Select style={{width:180}}>
                        <Option value='11'>ABC</Option>
                        <Option value='22'>22</Option>
                        <Option value='33'>33</Option>
                        <Option value='44'>44</Option>
                    </Select>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="客户姓名" >
                {getFieldDecorator('name', {
                    rules: [{ required: false, message: '请输入跟进内容!' }],
                })(
                    <Input placeholder="客户姓名" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="进客项目" >
                {getFieldDecorator('pro', {
                })(
                   <Input placeholder="进客项目" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="进客来源" >
                {getFieldDecorator('or', {
                })(
                    <Select style={{width:180}}>
                        <Option value='11'>线上推广1</Option>
                        <Option value='22'>22</Option>
                        <Option value='33'>33</Option>
                        <Option value='44'>44</Option>
                    </Select>
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="登记时间" >
                {getFieldDecorator('rtime', {
                
                })(
                    <DatePicker showTime placeholder="请选择登记时间" onChange={onChange} onOk={onOk} style={{width:170}} />
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="最近跟进时间" >
                {getFieldDecorator('ztime', {
                })(
                    <DatePicker showTime placeholder="请选择最近跟进时间" onChange={onChange} onOk={onOk} style={{width:170}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="提醒日期" >
                {getFieldDecorator('ttime', {
                    rules: [{ required: false, message: '请选择提醒日期!' }],
                })(
                    <DatePicker showTime placeholder="请选择最近跟进时间" onChange={onChange} onOk={onOk} style={{width:170}} />
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="登记人" >
                {getFieldDecorator('r', {
                    rules: [{ required: false, message: '请输入跟进内容!' }],
                })(
                    <Input placeholder="登记人" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="委托人" >
                {getFieldDecorator('wpeople', {
                    rules: [{ required: false, message: '请填写委托人!' }],
                })(
                    <Input  placeholder="委托人" style={{width:180}} />
  
                )}
                </Form.Item>
            </Col>
            
        </Row>

       
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms