import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Modal
  } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const { confirm } = Modal;
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function showConfirm() {
  confirm({
    title: '确定删除该权限吗?',
    content: '删除权限后不可恢复',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
class positionPower extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      handleSelectChange(){

      }
      tiaoDetail(num){
        this.props.history.push({pathname:'/personmanage/quanxianfenpei',data:num});
      }
      componentDidMount(){
       this.getPosition();

      }

      getPosition(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/role/index',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data.data;
             var arr=[];
             number.map((v,j)=>{
              arr.push({
                     key:v.id,
                     postname:v.name,
                     postmiaoshu:v.introduce,
                     num:"2",
                 })
                 this.setState({
                  datanum:arr,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
         
      }
      tiaolink(){
        this.props.history.push('/personmanage/addposition');
      }
      deletePosition(num){
            console.log(num);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
            axios.post('/index.php/role/delete',{
              id:num,
              name:user,
              num:loginid
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                console.log("删除成功")
                this.getPosition();
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
          }
      }
      checkQuanX=(num)=>{
            console.log(num);
            this.props.history.push({pathname:'/personmanage/checkquan',data:num});
      }
      render(){
        const columns = [
          {
            title: '岗位名称',
            dataIndex: 'postname',
          },
          {
            title: '岗位描述',
            dataIndex: 'postmiaoshu',
          },
          {
            title: '人数',
            dataIndex: 'num',
          },
          {
            title: '操作',
            dataIndex: 'handle',
            render:(text ,record) =><div>
               {/* <span onClick={this.tiaoDetail.bind(this,record.key)} style={{color:"#1890FF",cursor:"pointer"}}>分配权限</span>
            &nbsp; */}
            <span onClick={this.checkQuanX.bind(this,record.key)} style={{color:"#1890FF",cursor:"pointer"}}>编辑权限</span> 
            &nbsp;<span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.deletePosition.bind(this,record.key)}>删除</span></div>,
          },

        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          console.log(count);
           return `共 ${total} 条`;
        }),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item  ><Link to="/personmanage/stafflist"> 人员管理</Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/personmanage/positionpower">岗位权限设置 </Link></Breadcrumb.Item>
                </Breadcrumb>
             </div>
            <div className="project-table">
                      <Button type="primary"  style={{width:"100px",height:"32px",float:'right'}} onClick={this.tiaolink.bind(this)}>新增</Button>
                      <br/>
                      <br/>
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'3px'}} onClick={showConfirm} >删除</Button>
                    
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
export default positionPower