import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 LocaleProvider,
 Modal
  } from 'antd';
 import zhCN from 'antd/lib/locale-provider/zh_CN';
 import axios from 'axios'
 import '../../style/projectsuccess.scss'
 import { Link } from 'react-router-dom'
const loginid=localStorage.getItem('loginid');
const user=localStorage.getItem('name');
const {Option}=Select
const { TabPane } = Tabs;
const { confirm } = Modal;
const danprice=['0-7999','8000-9999','10000-14999','15000-19999','20000-24999','25000-29999','30000-39999','40000-99999',]
const zongjia=['0-100','100-150','150-200','200-250','250-300','300-350','400-450','450-500','500-']
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function showConfirm() {
  confirm({
    title: '确定删除该成交记录吗?',
    content: '删除成交记录后不可恢复',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          obj:[],
          priceNum:[],
          daikan:[],
          cheng:[],
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
              axios.post('/index.php/recording/recordProjectSou',{
                  name:user,
                  num:loginid,
                  value:values
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                    console.log(res);
                   var number=res.data.data;
                   var arr=[];
                   if(number.length>0){
                   number.map((v,j)=>{
                  arr.push({
                           key:v.id,
                           tag:`${v.building_xiaoshou}/${v.label}`,
                           projectname:v.building_name,
                           province:v.provice,
                           city:v.city,
                           volume:v.chengjiao,
                           wtstore:v.client_store,
                           wtpeople:v.client_people,
                           rtime:v.reg_time,
                       })
                       this.setState({
                        datanum:arr,
                        loading:false
                       })
                   })
                  }else{
                    this.setState({
                      datanum:[],
                      loading:false
                     })
                       
                  }
                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
            }
        
          }
        });
      };
      tiaolink(){
        this.props.history.push('/projectmanage/projectInformation/projectadd');
      }
      handleSelectChange(){

      }
      handleSelectChangePrice=(value)=>{
        if(value=='building_jiage'){
              this.setState({
                priceNum:danprice
              })
        }else if(value=='zongjia'){
          this.setState({
            priceNum:zongjia
          })
        }
       
    }
      tiaoDetail(num){
        this.props.history.push({pathname:'/projectmanage/projectInformation/projectdetail',data:num});
      }
      componentDidMount(){
        this.getVolumeProject();
        this.getArea();

    

      }
      getArea(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        axios.post('/index.php/pro/getarea',{
          num:loginid,
          name:user
        })
        .then((res)=>{
          var arr=res.data.data
          const list = Object.keys(arr).map((item, index) => ({ value:arr[item],label: item}));
          list.map(ite => {
            const shi = Object.keys(ite.value).map((j, index) => ({ value:ite.value[j],label: j}));
             this.state.obj.push({
              'value':ite.label,
              'label':ite.label,
              'children': shi.map(item=>{
                const qu= Object.keys(item.value).map((k, index) => ({ value:item.value[k],label: item.value[k]}));
                const  xian={
                  'value':item.label,
                  'label':item.label,
                  'children':qu
                };
                return xian;
                 })
                
            })
           
          })
        }).catch((error)=>{
        console.log(error)
        })
     
      }

    getVolumeProject(){
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
      console.log(loginid,user);
      if(loginid!='' && user!=""){
      axios.post('/index.php/recording/recordProject',{
          name:user,
          num:loginid
      })
      .then((res)=>{
        if(parseInt(res.status) === 200){
            console.log(res);
           var number=res.data.data;
           var arr=[];
           if(number.length>0){
           number.map((v,j)=>{
          arr.push({
                   key:v.id,
                   tag:`${v.building_xiaoshou}/${v.label}`,
                   projectname:v.building_name,
                   province:v.provice,
                   city:v.city,
                   volume:v.chengjiao,
                   wtstore:v.client_store,
                   wtpeople:v.client_people,
                   rtime:v.reg_time,
               })
               this.setState({
                datanum:arr,
                loading:false
               })
           })
          }else{
            this.setState({
              datanum:[],
              loading:false
             })
               
          }
        }else{
           console.log("请求数据失败")
        }
      }).catch((error)=>{
        console.log(error)
      })
    }

    }
    getDaiKan=()=>{
      var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
   var key= sessionStorage.getItem('volume');
      if(loginid!='' && user!=""){
        axios.post('/index.php/recording/recordProjectDai',{
            name:user,
            num:loginid,
            id:key
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
             var number=res.data.data;
             console.log(number);
             var arr=[];
             if(number.length>0){
             number.map((v,j)=>{
            arr.push({
                  key: v.id,
                  dtime: v.time,
                  dcontent: v.bei,
                  yperson:v.s_id,
                  rtype:v.type,
                 })
                 this.setState({
                  daikan:arr,
                  loading:false
                 })
             })
            }else{
              this.setState({
                daikan:[],
                loading:false
               })
                 
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
    }
    getVolume(){
      var key= sessionStorage.getItem('volume');
      var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
      if(loginid!='' && user!=""){
        axios.post('/index.php/recording/recordProjects',{
            name:user,
            num:loginid,
            id:key
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
             var number=res.data.data;
             console.log(number);
             var arr=[];
             if(number.length>0){
             number.map((v,j)=>{
            arr.push({
              key: v.id,
              cjdate: v.time,
              hetongnum: v.project,
              cjmoney: v.s_id,
              htmoney:v.from,
                 })
                 this.setState({
                  cheng:arr,
                  loading:false
                 })
             })
            }else{
              this.setState({
                cheng:[],
                loading:false
               })
                 
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
    }
    refresh(){
      console.log(1);
    window.location.reload()
    // this.props.history.push(0);
    }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { cheng } =this.state;
        const columns = [
          {
            title: '标签',
            dataIndex: 'tag',
           
          },
          {
            title: '项目名称',
            dataIndex: 'projectname',
            render:(text ,record) => <span onClick={this.tiaoDetail.bind(this,record.key)} style={{color:"#1890FF",cursor:"pointer"}}>{text}</span>,
          },
          {
            title: '所属省份',
            dataIndex: 'province',
          },
          {
            title: '城市',
            dataIndex: 'city',
          },
          {
            title: '成交量',
            dataIndex: 'volume',
          },
          {
            title: '委托门店',
            dataIndex: 'wtstore',
          },
          {
            title: '委托人',
            dataIndex: 'wtpeople',
          },
          {
            title: '登记时间',
            dataIndex: 'rtime',
          },
        ];
        const columns1 = [
          {
            title: '带看时间',
            dataIndex: 'dtime',
           
          },
          {
            title: '带看内容',
            dataIndex: 'dcontent',
          },
          {
            title: '业务员',
            dataIndex: 'yperson',
          },
          {
            title: '录入方式',
            dataIndex: 'rtype',
          },
        ];
        const columns2 = [
          {
            title: '成交时间',
            dataIndex: 'cjdate',
           
          },
          {
            title: '成交项目',
            dataIndex: 'hetongnum',
          },
          {
            title: '业务员',
            dataIndex: 'cjmoney',
          },
          {
            title: '录入方式',
            dataIndex: 'htmoney',
          },
        ];
        const data2=[];
        for (let i = 0; i < 5; i++) {
         data2.push({
           key: i,
           cjdate: '19-03-02',
           hetongnum: "KFC66866",
           cjmoney:"3600000.00",
           htmoney:'3600000.00',
           yezhufu:'0.00',
           kehufu:'3600000.00',
           shishou:'3600000.00',
           cjaddress:"林东北",
         });
       }

        const data1=[];
        for (let i = 0; i < 5; i++) {
         data1.push({
           key: i,
           dtime: '09-04-04 08：12',
           dcontent: "客户说不错",
           yperson:"林北",
           txtime:'09-03-11 03：23',
           rtype:"电脑端",
         });
       }
      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          console.log(count);
           return `共 ${total} 条`;
        }),
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        type:'radio',
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows,selectedRowKeys[0]);
          var key=selectedRowKeys[0];
          console.log(key);
          sessionStorage.setItem('volume',key);
         this.getDaiKan();
         this.getVolume();
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="projectsuccess">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item > <Link to="/projectmanage/projectInformation/projectlist">项目管理</Link></Breadcrumb.Item>
                  <Breadcrumb.Item > <Link to="/projectmanage/projectsuccess">项目成交管理</Link> </Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="类型">
              {getFieldDecorator('building_xingshi', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Select
                style={{ width:'120px'}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="公寓">公寓</Option>
                  <Option value="住宅">住宅</Option>
                  <Option value="商铺">商铺</Option>
                  <Option value="写字楼">写字楼</Option>
                </Select>,
              )}
            </Form.Item>  

            <Form.Item label="项目名称">
            {getFieldDecorator('building_name', {
            rules: [{ required: false, message: '请填写项目名称!' }],
          })(<Input   style={{ width:'200px',marginRight:"20px"}} />)}
            </Form.Item>

            <Form.Item label="区域" layout="inline">
            {getFieldDecorator('cate_id', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Cascader options={this.state.obj} onChange={onChange} placeholder="请选择"  style={{ width:'250px'}} />
              )}
            </Form.Item>
            
            <Form.Item label="价格" layout="inline">
            {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                 <Select 
                 placeholder="单价"
                  onChange={this.handleSelectChangePrice}
                  style={{width:"120px",marginRight:"9px"}}
                >
                  <Option value="building_jiage">单价</Option>
                  <Option value="zongjia">总价</Option>
                </Select> 
              )}
               {getFieldDecorator('num', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                  style={{width:"200px",marginRight:"20px"}}
                >
                  {
                       this.state.priceNum.map((j,k)=>{
                             return(
                              <Option value={j} key={k}>{j}</Option>
                             )
                       })
                   }
                </Select>
              )}
              </Form.Item>
              <Form.Item label="户型" >
              {getFieldDecorator('huxing', {
                rules: [{ required: false, message: '请选择户型' }],
              })(
                <Select style={{ width: '300px' }} 
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                  style={{width:"120px",marginRight:'56px'}}
                >
                   <Option value="1">一室</Option>
                  <Option value="2">二室</Option>
                  <Option value="3">三室</Option>
                  <Option value="4">四室</Option>
                  <Option value="5">五室及以上</Option>
                  <Option value="6">单卫</Option>
                  <Option value="7">双卫</Option>
                </Select>,
              )}
              </Form.Item>
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     <br/>
                     <br/>
                     {/* <p className="yema">共&nbsp;<span>10&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px',marginLeft:40}} onClick={showConfirm}>删除</Button>
                     <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px',}} onClick={this.refresh.bind(this)}>刷新</Button>
            </div>

            <div className="project-tab">
            <Tabs defaultActiveKey="1" onChange={callback}>
            <TabPane tab="带看情况" key="1">
                     <Table  columns={columns1} dataSource={this.state.daikan} />
                    
           
            </TabPane>
            <TabPane tab="成交情况" key="2">
                  <Table  columns={columns2} dataSource={cheng} />
            </TabPane>
          </Tabs>
            </div>

            

          </div>
          </LocaleProvider>
          )
      }
}
const projectSuccess = Form.create()(RegistForm);
export default projectSuccess