import React ,{Component} from 'react';
import{Breadcrumb,DatePicker,Form,Select,Cascader,Input,LocaleProvider } from 'antd'
import  '../../../style/statistics/volume.scss'
import {
  Chart,
  Geom,
  Tooltip,
  Coord,
  Label,
  View,
  Legend
} from 'bizcharts';
import DataSet from '@antv/data-set';
import zhCN from 'antd/lib/locale-provider/zh_CN';
const { MonthPicker, RangePicker } = DatePicker;
const {Option}= Select;
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
function onChange(value) {
  console.log(value);
}
class caipanForm extends Component{
      constructor(props){
        super(props)
        this.state={

        }
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const options = [
          {
            value: '浙江',
            label: '浙江',
            children: [
              {
                value: '杭州',
                label: '杭州',
              },
              {
                value: '宁波',
                label: '宁波',
              },
            ],
          },
          {
            value: '贵州',
            label: '贵州',
            children: [
              {
                value: '贵阳',
                label: '贵阳',
              },
              {
                value: '贵阳2',
                label: '贵阳2',
              },
            ],
          },
        ];
        const { DataView } = DataSet;
        const data = [
          {
            value: 251,
            type: '踩盘量',
            name: '踩盘量1',
          },
          {
            value: 1048,
            type: '踩盘量',
            name: '考盘量',
          },
          {
            value: 300,
            type: '未踩盘',
            name: '踩盘量2',
          },
          {
            value: 900,
            type: '未踩盘',
            name: '考盘量2',
          },
        
         
        ];
        const dv = new DataView();
        dv.source(data).transform({
          type: 'percent',
          field: 'value',
          dimension: 'type',
          as: 'percent',
        });
        const cols = {
          percent: {
            formatter: (val) => {
              val = `${(val * 100).toFixed(2)}%`;
              return val;
            },
          },
        };
        const dv1 = new DataView();
        dv1.source(data).transform({
          type: 'percent',
          field: 'value',
          dimension: 'name',
          as: 'percent',
        });
        return(
          <LocaleProvider locale={zhCN}>
          <div className="volume">
                <Breadcrumb>
              <Breadcrumb.Item href="/"> 统计分析</Breadcrumb.Item>
              <Breadcrumb.Item href=""> 成交量 </Breadcrumb.Item>
          </Breadcrumb>
              <div className="option">
                <Form layout="inline">
              <Form.Item label="区域" layout="inline">
            {getFieldDecorator('address1', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select
                 style={{width:"150px",marginRight:"9px"}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="type1">浙江</Option>
                  <Option value="type2">贵州</Option>
                </Select>,
              )}
               {getFieldDecorator('address2', {
                rules: [{ required: false, message: '请选择区' }],
              })(
                <Cascader options={options} onChange={onChange} placeholder="请选择"   style={{width:"150px",marginRight:"20px"}} />,
              )}
            </Form.Item>
            <Form.Item label="人员">
            {getFieldDecorator('person', {
                rules: [{ required: false, message: '请输入人员' }],
              })(
                <Input placeholder="请输入"   style={{width:"150px",marginRight:"150px"}} />,
              )}
            </Form.Item>
            <Form.Item>
            <ul className="time">
                        <li>日</li>
                        <li>周</li>
                        <li>月</li>
                        <li>季度</li>
                        <li>年度</li>
                    </ul>
            </Form.Item>
            <Form.Item>
            <RangePicker onChange={onChangeDate} />
          </Form.Item>
            </Form>
        </div>   
            
        <Chart
          height={window.innerHeight}
          data={dv}
          scale={cols}
          padding={[80, 100, 80, 80]}
          forceFit
        >
          <Coord type="theta" radius={0.5} />
          <Tooltip
            showTitle={false}
            itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
          />
          <Geom
            type="intervalStack"
            position="percent"
            color="type"
            tooltip={[
              'type*percent',
              (item, percent) => {
                percent = `${(percent * 100).toFixed(2)}%`;
                return {
                  name: item,
                  value: percent,
                };
              },
            ]}
            style={{
              lineWidth: 1,
              stroke: '#fff',
            }}
            select={false}
          >
            <Legend position="right-top"/>
            <Label content="type" offset={-10} />
          </Geom>
          <View data={dv1} scale={cols}>
            <Coord type="theta" radius={0.75} innerRadius={0.5 / 0.75} />
            <Geom
              type="intervalStack"
              position="percent"
              color={[
                'name',
                [
                  '#BAE7FF',
                  '#7FC9FE',
                  '#71E3E3',
                  '#ABF5F5',
                  '#8EE0A1',
                  '#BAF5C4',
                ],
              ]}
              tooltip={[
                'name*percent',
                (item, percent) => {
                  percent = `${(percent * 100).toFixed(2)}%`;
                  return {
                    name: item,
                    value: percent,
                  };
                },
              ]}
              style={{
                lineWidth: 1,
                stroke: '#fff',
              }}
              select={false}
            >
              <Label content="name" />
            </Geom>
          </View>
        </Chart>

          </div>
          </LocaleProvider>
          )
      }
}
const caipanliang=Form.create()(caipanForm)
  export default caipanliang