import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Modal
  } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const { confirm } = Modal;
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
function showConfirm() {
  confirm({
    title: '确定删除该权限吗?',
    content: '删除权限后不可恢复',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
class teldetailForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      handleSelectChange(){

      }
      tiaoDetail(num){
        this.props.history.push({pathname:'/personmanage/quanxianfenpei',data:num});
      }
      componentDidMount(){
       this.getPosition();
       var key=this.props.location.data;
       console.log(key);
      }

      getPosition(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
          var key=this.props.location.data;
        if(loginid!='' && user!=""){
        axios.post('/index.php/gen/tellist',{
          name:user,
          num:loginid,
          ids:key
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data.data;
             var arr=[];
             if(number.length>0){
             number.map((v,j)=>{
              arr.push({
                     key:v.id,
                     time:v.create_time,
                     content:v.content,
                     wei:v.wei,
                     deng:v.deng[0],
                 })
                 this.setState({
                  datanum:arr,
                  loading:false
                 })
             })
            }else{
              this.setState({
                datanum:'',
                loading:false
               })
            }
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
         
      }
      tiaolink(){
        this.props.history.push('/personmanage/addposition');
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
          }
        });
      };
    
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '跟进时间',
            dataIndex: 'time',
          },
          {
            title: '跟进内容',
            dataIndex: 'content',
          },
        //   {
        //     title: '业务员',
        //     dataIndex: 'ye',
        //   },
          {
            title: '委托人',
            dataIndex: 'wei',
          },
          {
            title: '登记人',
            dataIndex: 'deng',
          },
         
        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total) => {
            return `共 ${total} 条`;
          }),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item  ><Link to="/statistics/telfollow"> 统计分析</Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/statistics/telfollow">电话跟进 </Link></Breadcrumb.Item>
                  <Breadcrumb.Item ><Link to="/statistics/telfollowdetail">电话跟进详情</Link> </Breadcrumb.Item>
                </Breadcrumb>
             </div>
             <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               <Form.Item label="区域">
              {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Select
                style={{ width:'150px',marginRight:40}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="公寓">公寓</Option>
                  <Option value="住宅">住宅</Option>
                  <Option value="商铺">商铺</Option>
                  <Option value="写字楼">写字楼</Option>
                </Select>,
              )}
            </Form.Item>  
            <Form.Item label="人员">
              {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Input
                style={{ width:'150px',marginRight:40}}
                  placeholder="所有"
                />
              )}
            </Form.Item> 
            <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button> 
            </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'3px'}} onClick={showConfirm} >删除</Button>
                    
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const teldetail=Form.create()(teldetailForm);
export default teldetail