import React ,{Component} from 'react';
import{Breadcrumb,Form,Input,Button,Table,LocaleProvider } from 'antd'
import axios from 'axios'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import '../../../style/statistics/caipanlist.scss'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class caidetailForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        //项目列表
        if(loginid!='' && user!=""){
        axios.post('/index.php/pro/index',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     projectname:v.building_name,
                     cainum:'44',
                     kaonum:'35',
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
     //区域数据
      }
      
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '项目名称',
            dataIndex: 'projectname',
          },
          {
            title: '踩盘人数',
            dataIndex: 'cainum',
          },
          {
            title: '考盘人数',
            dataIndex: 'kaonum',
          },
          {
            title: '操作',
            dataIndex: 'handle',
            render:(text ,record) => <div><span style={{color:"#1890FF",cursor:"pointer"}}>详情</span>&nbsp;<span style={{color:"#1890FF",cursor:"pointer"}}>删除</span></div>
          }

        ];
        const rowSelection = {
          onChange: (selectedRowKeys, selectedRows) => {
            console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          },
          getCheckboxProps: record => ({
            disabled: record.name === 'Disabled User', // Column configuration not to be checked
            name: record.name,
          }),
        };
        const paginationProps = {
          showSizeChanger: true,
          showQuickJumper: {goButton:<Button type="primary">确定</Button>},
          showTotal: ((total,count) => {
            console.log(count);
             return `共 ${total} 条`;
          }),
          // showTotal: () => `共${totals}条`,
          // pageSize: this.state.pageSize,
          // current: page.pageNum,
          // total: page.total,
          // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
          // onChange: (current) => this.changePage(current),
        };
        return(
          <LocaleProvider locale={zhCN}>
          <div className="caipanlist">
            <Breadcrumb>
              <Breadcrumb.Item href="/"> 统计分析</Breadcrumb.Item>
              <Breadcrumb.Item href="/">踩盘量</Breadcrumb.Item>
              <Breadcrumb.Item href=""> 踩盘量详情 </Breadcrumb.Item>
          </Breadcrumb>
          <Form layout="inline">
            <Form.Item label="项目名称">
                {getFieldDecorator('projectname',{
                    rules: [{ required: false, message: '请输入' }],
                })(
                  <Input placeholder="请输入" style={{ width:150}}/>
                )}
            </Form.Item>
            <Form.Item>
                 <Button type="primary" style={{marginRight:'944px'}}>查询</Button>
            </Form.Item>
            <Form.Item>
                <span className="num">踩盘：<i>44个</i></span>
                <span className="num">考盘：<i>39个</i></span>
            </Form.Item>
          </Form>
              <div className="tablelist">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   loading={this.state.loading}  pagination={ paginationProps } />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i style={{fontStyle:'normal'}}>&nbsp;1/50</i>&nbsp;页</p> */}
              </div>
          
          </div>
          </LocaleProvider>
          )
      }
}
const caipanList=Form.create()(caidetailForm);
  export default caipanList