import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,

} from 'antd';
import '../../../style/addbuyhousehelp.scss'
import axios from 'axios'
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={

        }
        
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key= sessionStorage.getItem('wentype');
            console.log(key);
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            axios.post('/index.php/zhi/save',{  
                value:values,
                type:key,
                name:user,
                num:loginid
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                 console.log(res);
                 this.props.history.push('/projectmanage/projectInformation/buyhousehelp');
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
          }
          }
        });
      };
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
    <div className="addbuyhousehelp">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">楼盘资讯 </Breadcrumb.Item>
            <Breadcrumb.Item href="">添加买房指南 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">问：</p>
            <Form.Item >
            {getFieldDecorator('title', {
                })(
            <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} />,
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">答：</p>
            <Form.Item >
            {getFieldDecorator('content', {
                })(
            <TextArea rows={4}   autosize={{ minRows: 10, maxRows: 13 }}  className="answer"/>
            )}
            </Form.Item >
        </Row>
        <br/>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          上传
          </Button>
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou