import React ,{Component} from 'react';
import axios from 'axios'
const  loginid=localStorage.getItem('loginid');
const user=localStorage.getItem('name');
const getallArea=()=>{
    if(loginid!='' && user!=""){
        axios.post('/index.php/pro/getarea',{
          num:loginid,
          name:user
        })
        .then((res)=>{
          var arr=res.data.data
          const list = Object.keys(arr).map((item, index) => ({ value:arr[item],label: item}));
          list.map(ite => {
            const shi = Object.keys(ite.value).map((j, index) => ({ value:ite.value[j],label: j}));
             this.state.obj.push({
              'value':ite.label,
              'label':ite.label,
              'children': shi.map(item=>{
                const qu= Object.keys(item.value).map((k, index) => ({ value:item.value[k],label: item.value[k]}));
                const  xian={
                  'value':item.label,
                  'label':item.label,
                  'children':qu
                };
                return xian;
                 })
                
            })
           
          })
          console.log(this.state.obj);
        }).catch((error)=>{
        console.log(error)
        })
      }
}
  export default getallArea