import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
 Table,
 Icon,
 LocaleProvider,
 Menu, 
 Dropdown, 
} from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
import getallArea from '../common/getArea'
const {Option}=Select
const { TabPane } = Tabs;
const danprice=['0-1万','1万-2万','2万-3万','3万-4万','4万-5万','5万以上']
const zongjia=['100万以内','100-150万','150-200万','200-300万','300-500万','500万以上']
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
class staffForm extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
          obj:[],
          priceNum:[],
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
          var id=  sessionStorage.getItem('cjids');
            console.log('Received values of form: ', values);
            if(loginid!='' && user!=""){
              var loginid=localStorage.getItem('loginid');
              var user=localStorage.getItem('name');
              axios.post('/index.php/recording/projectSou',{
                name:user,
                num:loginid,
                value:values,
                ids:id
              })
              .then((res)=>{
                if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  var number=res.data.data;
                  var arr=[];
                  if(number.length>0){
                    number.map((v,j)=>{
                      arr.push({
                            key:v.id,
                            cjnnum:v.id,
                            cjproject:v.project,
                            cjtime:v.time,
                            cjmoney:v.money,
                            yeji:v.yeji,
                        })
                        this.setState({
                         datanum:arr,
                         loading:false
                        })
                    })
                  }else{
                    this.setState({
                      datanum:'',
                      loading:false
                     })
                  }
                 
                }else{
                   console.log("请求数据失败")
                }
              }).catch((error)=>{
                console.log(error)
              })
            }

          }
        });
      };
      handleSelectChange(){

      }
      getMyVolume(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
          axios.post('/index.php/recording/project',{
            name:user,
            num:loginid
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log(res);
              console.log(res.data.ids);
              sessionStorage.setItem('cjids',res.data.ids);
               var number=res.data.data
               if(number.length>0){
            
               number.map((v,j)=>{
               this.state.datanum.push({
                       key:v.id,
                       cjnnum:v.id,
                       cjproject:v.project,
                       cjtime:v.time,
                       cjmoney:v.money,
                       yeji:v.yeji,
                   })
               var  datanum=this.state.datanum
                   this.setState({
                    datanum:datanum,
                    loading:false
                   })
               })
                    
              }else{
                this.setState({
                  datanum:'',
                  loading:false
                 })
              }
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      getallArea=()=>{
      if(loginid!='' && user!=""){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        axios.post('/index.php/pro/getarea',{
          num:loginid,
          name:user
        })
        .then((res)=>{
          var arr=res.data.data
          const list = Object.keys(arr).map((item, index) => ({ value:arr[item],label: item}));
          list.map(ite => {
            const shi = Object.keys(ite.value).map((j, index) => ({ value:j,label: j}));
             this.state.obj.push({
              'value':ite.label,
              'label':ite.label,
              'children': shi
            })
           
          })
          console.log(this.state.obj);
        }).catch((error)=>{
        console.log(error)
        })
      }
    }
      tiaoDetail(num){
        console.log(num);
        this.props.history.push({pathname:'/personInformation/chengjiaodetail',data:num});
      }
      componentDidMount(){
       this.getMyVolume();
       this.getallArea();

      }
      tiaolink(){
        this.props.history.push('/personmanage/addstaff');
      }
      deletelist(num){
        console.log(num);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
          axios.post('/index.php/recording/delete',{
            name:user,
            num:loginid,
            id:num
          })
          .then((res)=>{
            if(parseInt(res.status) === 200){
              console.log(res);
             this.getMyVolume();
            }else{
               console.log("请求数据失败")
            }
          }).catch((error)=>{
            console.log(error)
          })
        }
      }
      handleSelectChangePrice=(value)=>{
        if(value=='building_jiage'){
              this.setState({
                priceNum:danprice
              })
        }else if(value=='zongjia'){
          this.setState({
            priceNum:zongjia
          })
        }
       
    }
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '成交房源编号',
            dataIndex: 'cjnnum',
          },
          {
            title: '成交项目',
            dataIndex: 'cjproject',
          },
          {
            title: '成交时间',
            dataIndex: 'cjtime',
          },
          {
            title: '成交金额',
            dataIndex: 'cjmoney',
          },
          {
            title: '业绩',
            dataIndex: 'yeji',
          },
          {
            title: '操作',
            dataIndex: 'handal',
            render:(text,record)=><div><span style={{color:'rgba(24,144,255,1)'}} onClick={this.tiaoDetail.bind(this,record.key)}>详情</span>&nbsp;| &nbsp;<span style={{color:'rgba(24,144,255,1)'}} onClick={this.deletelist.bind(this,record.key)}>删除</span></div>
          },
         
        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          console.log(count);
           return `共 ${total} 条`;
        }),
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item ><Link to="/personInformation/TransactionStatus"> 个人信息</Link></Breadcrumb.Item>
                  <Breadcrumb.Item><Link to="/personInformation/TransactionStatus">成交情况 </Link></Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
               {/* <Form.Item label="状态">
              {getFieldDecorator('status', {
                rules: [{ required: false, message: '请选择类型!' }],
              })(
                <Select
                style={{ width:'150px',marginRight:40}}
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                >
                  <Option value="有效">有效</Option>
                  <Option value="住宅">住宅</Option>
                  <Option value="商铺">商铺</Option>
                  <Option value="写字楼">写字楼</Option>
                </Select>,
              )}
            </Form.Item>   */}
         
            <Form.Item label="区域" layout="inline">
            {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Cascader options={this.state.obj} onChange={onChange} placeholder="请选择"  style={{ width:'250px'}} />
              )}
            </Form.Item>

            <Form.Item label="单价" layout="inline">
            {getFieldDecorator('type', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                 <Select
                  placeholder="所有"
                  onChange={this.handleSelectChangePrice}
                  style={{width:"120px",marginRight:"9px"}}
                >
                  <Option value="building_jiage">单价</Option>
                  <Option value="zongjia">总价</Option>
                </Select> 
              )}
               {getFieldDecorator('num', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                 <Select
                 placeholder="单价"
                 onChange={this.handleSelectChange}
                 style={{width:"200px",marginRight:"20px"}}
               >
                   {
                       this.state.priceNum.map((j,k)=>{
                             return(
                              <Option value={k+1} key={k}>{j}</Option>
                             )
                       })
                   }
               </Select>
              )}
              </Form.Item>


              <Form.Item label="户型" >
              {getFieldDecorator('huxing', {
                rules: [{ required: false, message: '请选择户型' }],
              })(
                <Select style={{ width: '300px' }} 
                  placeholder="所有"
                  onChange={this.handleSelectChange}
                  style={{width:"120px",marginRight:'56px'}}
                >
                  <Option value="1">一室</Option>
                  <Option value="2">二室</Option>
                  <Option value="3">三室</Option>
                  <Option value="4">四室</Option>
                  <Option value="5">五室及以上</Option>
                  <Option value="6">单卫</Option>
                  <Option value="7">双卫</Option>
                </Select>,
              )}
              </Form.Item>
           
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     <br/>
                     <br/>
                     <br/>
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'3px'}} >删除</Button>
                   
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const staffList = Form.create()(staffForm);
export default staffList