function showConfirm() {
  confirm({
    title: '公客转私客',
    content: '确定把该客户转为私客吗？',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}


function showConfirmsi() {
  confirm({
    title: '私客转公客',
    content: '确定把该客户转为公客吗？',
    onOk() {
      return new Promise((resolve, reject) => {
        setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
      }).catch(() => console.log('Oops errors!'));
    },
    onCancel() {},
  });
}
