import React ,{Component} from 'react';
import { 
    Form, 
    Input, 
    Button, 
    Breadcrumb ,
    Col,
    Row,
    Upload,
     Icon, 
     Modal,
     Select
  
  } from 'antd';
  import ReactQuill from 'react-quill'
  import 'react-quill/dist/quill.snow.css' 
  import '../../../style/projectzixunadd.scss'
  import axios from 'axios'
  const {Option}=Select
  var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
  function getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  
class projectZixun extends Component{
      constructor(props){
        super(props)
        this.state={
          previewVisible: false,
          previewImage: '',
          fileList: [],
          text: '',
          bindlou:[],

          detail:[],


        }
         this.handleChange = this.handleChange.bind(this)
      }

      modules = {
        toolbar: [
          [{ 'header': [1, 2, false] }],
          ['bold', 'italic', 'underline','strike', 'blockquote'],
          [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
          ['link', 'image'],
          ['clean']
        ],
      };
    
      formats = [
        'header',
        'bold', 'italic', 'underline', 'strike', 'blockquote',
        'list', 'bullet', 'indent',
        'link', 'image'
      ];
      componentDidMount(){
        //绑定楼盘
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
            axios.post('/index.php/info/create',{
              name:user,
          num:loginid
        })
        .then((res)=>{
             if(res.data.code==200){
                console.log(res.data.data);
                this.setState({
                   bindlou:res.data.data
                })
              }
        }).catch((error)=>{
          console.log(error)
        })
      }
      this.getDetail();
    }
      handleChange(html) {
       this.setState({ text: html })
        // console.log(html);
        // console.log(this.state.text)
      }
    

      handleCancel = () => this.setState({ previewVisible: false });
      handlePreview = async file => {
        if (!file.url && !file.preview) {
          file.preview = await getBase64(file.originFileObj);
        }
    
        this.setState({
          previewImage: file.url || file.preview,
          previewVisible: true,
        });
      };
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            if(loginid!='' && user!=""){
              var key=this.props.location.data;
              var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
                    axios.post('/index.php/info/update',{
                      bid:values.bid,
                      title:values.title,
                      content: this.state.text,
                      name:user,
                      num:loginid,
                      id:key
                })
                .then((res)=>{
                      if(res.data.code==200){
                        console.log(res);
                        this.props.history.push('/projectmanage/projectInformation/loupannews');
                      }
                }).catch((error)=>{
                  console.log(error)
                })
              }
          }
        });
      };
      getDetail=()=>{
            var key=this.props.location.data;
            console.log(key);
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            if(loginid!='' && user!=""){
                    axios.post('/index.php/info/edit',{
                        id:key,
                        name:user,
                        num:loginid
                })
                .then((res)=>{
                      if(res.data.code==200){
                         console.log(res.data.data);
                         var data=res.data.data;
                      
                                this.setState({
                                      detail:data,
                                      text:data.content
                                })
                      }
                }).catch((error)=>{
                  console.log(error)
                })
              }

      }


      handleChangePic = ({ fileList }) => this.setState({ fileList });
     
      render(){
        const { detail } =this.state;
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { previewVisible, previewImage, fileList,bindlou } = this.state;
        const uploadButton = (
          <div>
            <Icon type="plus" />
            <div className="ant-upload-text">上传</div>
          </div>
        );
        return(
          <div className="projectzixunadd">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">楼盘咨询 </Breadcrumb.Item>
            <Breadcrumb.Item href="">项目资讯 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
           <Form.Item label="资讯标题" >
            {getFieldDecorator('title', {
                  initialValue:detail.title
                })(
            <Input  style={{ width:750}} placeholder="请输入"/>,
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
           <Form.Item label="绑定楼盘" >
            {getFieldDecorator('bid', {
               initialValue:detail.bid
                })(
              <Select  style={{ width:750}}>
                  { bindlou.map((m,n)=>{
                      return(
                      <Option value={m.id} key={n}>{m.building_name}</Option>)
                  })}
              </Select>
                )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
        <div className="content">
            <Form.Item label="资讯内容" placeholder="请输入" >
            {/* {getFieldDecorator('zixun-neirong', {
                })( */}
                <ReactQuill modules={this.modules} formats={this.formats} style={{width:750, height:450 }} value={this.state.text} onChange={this.handleChange}></ReactQuill>
                {/* )} */}
            </Form.Item >
            </div>
        </Row>
        <br/>
        <br/>
        <br/>
        <br/>
       
      
        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          提交
          </Button>
        </Form.Item>
          </Form>
          </div>
          )
      }
}
const projectZixunAdd = Form.create()(projectZixun);
export default projectZixunAdd