import React ,{Component} from 'react';
import {Breadcrumb,Table,LocaleProvider,Button} from 'antd'
import axios from 'axios'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import '../../../style/statistics/volumedetail.scss'
import { Link } from 'react-router-dom'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class volume extends Component{
      constructor(props){
        super(props)
        this.state={
            datanum:[],
            loading:true,
        }
      }
      componentDidMount(){
        var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
        //项目列表
        if(loginid!='' && user!=""){
        axios.post('/index.php/recording/project',{
          name:user,
          num:loginid
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data.data
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     housenum:v.id,
                     cjpro:v.project,
                     cjdate:v.time,
                     cjmoney:v.money,
                     yeji:v.yeji,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
     //区域数据
      }
      godetail(num){
           this.props.history.push({pathname:'/statistics/volumedetail',data:num})
      }
      
      render(){
        const columns = [
            {
              title: '成交房源编号',
              dataIndex: 'housenum',
            },
            {
              title: '成交项目',
              dataIndex: 'cjpro',
            },
            {
              title: '成交日期',
              dataIndex: 'cjdate',
            },
            {
              title: '成交金额',
              dataIndex: 'cjmoney',
            },
            {
              title: '业绩',
              dataIndex: 'yeji',
            },
            {
              title: '操作',
              dataIndex: 'handle',
              render:(text ,record) => <span style={{color:"#1890FF",cursor:"pointer"}} onClick={this.godetail.bind(this,record.key)}>详情</span>
            }
  
          ];
          const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
              console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
            },
            getCheckboxProps: record => ({
              disabled: record.name === 'Disabled User', // Column configuration not to be checked
              name: record.name,
            }),
          };
          const paginationProps = {
            showSizeChanger: true,
            showQuickJumper: {goButton:<Button type="primary">确定</Button>},
            showTotal: ((total) => {
              return `共 ${total} 条`;
            }),
            // showTotal: () => `共${totals}条`,
            // pageSize: this.state.pageSize,
            // current: page.pageNum,
            // total: page.total,
            // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
            // onChange: (current) => this.changePage(current),
          };
        return(
            <LocaleProvider locale={zhCN}>
          <div className="volumedetail">
            <div className="bread">
            <Breadcrumb>
                <Breadcrumb.Item ><Link to="/statistics/volume"> 统计分析</Link></Breadcrumb.Item>
                <Breadcrumb.Item><Link to="/statistics/volume"> 成交量 </Link></Breadcrumb.Item>
                <Breadcrumb.Item><Link to="/statistics/volumelist">成交量列表</Link> </Breadcrumb.Item>
            </Breadcrumb>
            </div>
            <div className="formbox">
          {/* 客户基本信息*/}
          <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   loading={this.state.loading}  pagination={ paginationProps } />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i style={{fontStyle:'normal'}}>&nbsp;1/50</i>&nbsp;页</p> */}
            </div>

        </div>


          </div>
          </LocaleProvider>
          )
      }
}
  export default volume