import React ,{Component} from 'react';
import { Form, Icon, Input, Button, Checkbox,message } from 'antd';
import bgImg from '../../img/login.png'
import $ from 'jquery'
import axios from 'axios'
import '../../style/login/login.scss'
class LoginForm extends Component{
      constructor(props){
        super(props)
      }
      getCode(){
        var name=$('#username').val();
        axios.post('/index.php/login/getcode',{ 
          name:name
        })
        .then((res)=>{
          console.log(res);
          if(res.data.code==200){
            message.success('验证码已发送，请注意查收');
          }else if(res.data.code==300){
            message.error('验证码发送失败');
          }else if(res.data.code==10003){
            message.error('发送太频繁，稍后再试');
          }
         
        }).catch((error)=>{
             console.log(error)
        })

      }
      login(){
      // console.log($('#username').val())
      var username=$('#username').val();
      var password=$('#password').val();
      var yan=$('#yanzheng').val()
      const values={
        name:username,
        password:password,
        ma:yan
      }
      // console.log(values)
      if(yan!==''){
      axios.post('/index.php/login/login',{ 
           value:values
         })
         .then((res)=>{
           if(res.data.code==200){
           console.log(res);
           var id=res.data.re.id;
           console.log(id);
          //  localStorage.setItem('login',id);
           if(res.data.code==200){
         
            console.log(res.data.num,);
            var user=res.data.re.name
            var loginid=res.data.num
          localStorage.setItem('loginid',loginid);
          localStorage.setItem('name',user);
           this.props.history.push('./home');
           window.location.reload()
           }
          }else if(res.data.code==1001) {
            message.error('验证码错误');
          }else if(res.data.code==300){
            message.error('用户名或密码错误');
          }
         }).catch((error)=>{
            console.log(error)
         })
       }else{
        message.error('请输入验证码');
       }
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const bg={
          width: "100%",
          height: 976,
          backgroundImage: `url(${bgImg})`,
          
        }
        return(
          <div className="login"  style={bg}>
              <div className="loginbox">
                <h1> ERP系统</h1>
                 <form>
                       <div className="box"  style={{width:360,height:59,borderBottom:'1px solid rgba(230,230,230,1)'}}>
                            <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' ,fontSize:20}} />
                            <input type="text"  id="username"  placeholder="请输入姓名" required />
                       </div>
                       <div className="box" style={{width:360,height:59,borderBottom:'1px solid rgba(230,230,230,1)'}}>
                          <Icon type="lock"  style={{ color: 'rgba(0,0,0,.25)',fontSize:20}}  />
                          <input type="password" id="password" placeholder="请输入密码" required/>
                       </div>
                       <div  className="box" style={{width:360,height:59,borderBottom:'1px solid rgba(230,230,230,1)',marginBottom:60}}>
                          <Icon   type="key" style={{ color: 'rgba(0,0,0,.25)' ,fontSize:20 }} />
                          <input type="text"  id="yanzheng" className="yan"   placeholder="请输入验证码" required/>
                          <a href="javascript:;" style={{backgroundColor:'rgba(255,220,47,1)',padding:'7px 5px',color:'#fff',borderRadius:'2px'}} onClick={this.getCode.bind(this)}>获取验证码</a>
                       </div>
                       {/* <div  style={{fontSize:'18px',backgroundColor:'rgba(31,126,254,1)',borderRadius:'6px'}}> */}
                           <a href="javascript:;"   style={{fontSize:'18px',backgroundColor:'rgba(31,126,254,1)',borderRadius:'6px',padding:" 8px 162px",color:'#fff'  }} onClick={this.login.bind(this)}>登录</a>
                       {/* </div> */}
                      
                      
                 </form>
              </div>


          </div>
          )
      }
}
const Login=Form.create()(LoginForm)
  export default Login