import React ,{Component} from 'react';
import { 
  Form, 
  Button, 
  Breadcrumb ,
  Col,
  Row,
 Select,
 Checkbox,
 Table,
 Cascader ,
 DatePicker,
 Input,
 Upload, 
 Icon,
  Modal
} from 'antd';
import axios from 'axios'
import {Link} from 'react-router-dom'
import './../../style/newaddsell.scss'
const {Option}=Select
const fileList=[]
const props2 = {
  action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
  listType: 'picture',
  defaultFileList: [...fileList],
  className: 'upload-list-inline',
};
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChangeDate(date, dateString) {
  console.log(date, dateString);
}
function onOk(value) {
    console.log('onOk: ', value);
}
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
           position:[],
           leader:[],

           cheng:[],
           qu:[],
           zu:[],
        }
      }
     
  
  
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('输入的结果为 ', values);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
         axios.post('/index.php/staff/save',{ 
           value:values,
           name:user,
           num:loginid
            })
            .then((res)=>{
              console.log(res);
              if(res.data.code==200){
                 this.props.history.push('/personmanage/stafflist');
              }else {
              
              }
            }).catch(error=>{
              console.log(error)
            })
          }
      }
    });
  };
  handleDengChange=(value)=>{
      console.log(value);
      if(loginid!='' && user!=""){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
      axios.post('/index.php/staff/list',{ 
        id:value,
        name:user,
        num:loginid
      })
      .then((res)=>{
       
        if(res.data.code==200){
         console.log(res.data.data);
         
         this.setState({
          leader:res.data.data
         })
        }else {
        }
      }).catch(error=>{
        console.log(error)
      })
    }
  }
  getPosition(){
    if(loginid!='' && user!=""){
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
    axios.post('/index.php/role/list',{ 
      name:user,
      num:loginid
       })
       .then((res)=>{
        
         if(res.data.code==200){
          console.log(res.data.data);
          this.setState({
            position:res.data.data
          })
         }else {
         
         }
       }).catch(error=>{
         console.log(error)
       })
      }
  }
  getCity(){
    if(loginid!='' && user!=""){
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
    axios.post('/index.php/qu/one',{ 
      name:user,
      num:loginid
       })
       .then((res)=>{
         if(res.data.code==200){
          console.log(res.data.data);
          var city=res.data.data;
          if(city){
              this.setState({
                cheng:city
              })
          }
         
         }else {
         
         }
       }).catch(error=>{
          console.log(error)
       })
      }
  }


  componentDidMount(){
       this.getPosition();
       this.getCity();
  }
  handleSelectCity(value){
        console.log(value);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/two',{ 
          name:user,
          num:loginid,
          id:value
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var qu=res.data.data;
              if(qu){
                  this.setState({
                    qu:qu
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
             console.log(error)
           })
          }

  }
  handleSelectQu(value){
    console.log(value);
    if(loginid!='' && user!=""){
      var loginid=localStorage.getItem('loginid');
      var user=localStorage.getItem('name');
    axios.post('/index.php/qu/two',{ 
      name:user,
      num:loginid,
      id:value
       })
       .then((res)=>{
         if(res.data.code==200){
          console.log(res.data.data);
          var zu=res.data.data;
          if(zu){
              this.setState({
                zu:zu
              })
          }
         
         }else {
         
         }
       }).catch(error=>{
         console.log(error)
       })
      }


  }


  validFunction = (rule, val, callback) => {
    // console.log(val);
    if (!val) {
    callback();
    }
    let validateResult =/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57]|19[0-9]|16[0-9])[0-9]{8}$/ ; 
    let wei=/\b\d{12}\b/
    let other= /^[1]((3[0-9])|(4[5,7,9])|(5[4])|(8[0-9])|(7[0,1,3,5,6,7,8]))\d{8}$/;
    var result=(!validateResult.test(val));
    var res=!wei.test(val);
    var ress=!other.test(val)
    console.log(result, res);
    if(result && res && ress){
    callback('请输入正确手机号或座机号！'); 
    }
    callback();
    }
  
      render(){
        const { getFieldDecorator } = this.props.form;
        const { position ,leader,cheng,qu,zu } =this.state
        return(
          <div className="newaddsell">
              <div className="bread">
              <Breadcrumb>
                  <Breadcrumb.Item href="/"> 人员管理</Breadcrumb.Item>
                  <Breadcrumb.Item href=""> 员工列表 </Breadcrumb.Item>
              </Breadcrumb>
              </div>
              <Form layout="inline" onSubmit={this.handleSubmit}>
                  {/*基本信息 */}
        <p className="title1 ">基本信息</p>
        <br/>
         <Row  gutter={24} >
         <Col span={6}>
        <Form.Item label="姓名">
          {getFieldDecorator('name', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input
            style={{ width:'200px'}}
              placeholder="请输入"
            />
             
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="性别">
          {getFieldDecorator('sex', {
            rules: [{ required: false, message: '请选择!' }],
          })(
            <Select
            style={{ width:'200px'}}
              placeholder="请选择"
              onChange={this.handleSelectChange}
            >
              <Option value="男">男</Option>
              <Option value="女">女</Option>
              
            </Select>,
          )}
          
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="身份证号">
          {getFieldDecorator('card_id', {
            rules: [{ required: false, message: '请输入!' },
            {pattern:  /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/,message:'请输入正确身份证'}
           
            ],
          })(
            <Input  style={{ width:'200px'}}
            placeholder="请输入" />
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="学历">
          {getFieldDecorator('education', {
            rules: [{ required: false, message: '请输入' }],
          })(
           <Input style={{width:200}}/>
          )}
        </Form.Item>
        </Col>
        </Row>
         <br />
        <Row gutter={24} >
        <Col span={6}>
        <Form.Item label="专业">
          {getFieldDecorator('profession', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="城市">
            {getFieldDecorator('city', {
                rules: [{ required: false, message: '请输入' }],
            })(
              <Select  style={{ width:200}} onChange={this.handleSelectCity.bind(this)}>
                {
                    cheng.length>0?cheng.map((m,n)=>{
                         return(
                            <Option value={m.id} key={m.id}>{m.name}</Option>
                         )
                    }) : ''
                }
              </Select>
            )}
        </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="区域">
            {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择!' }],
            })(
              <Select  style={{ width:200}} onSelect={this.handleSelectQu.bind(this)}>
                {
                     qu.length>0?qu.map((m,n)=>{
                           return(
                            <Option value={m.id} key={n}>{m.name}</Option>
                           )
                     }) :''
                }
              </Select>
            )}
        </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="组别">
            {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择!' }],
            })(
           

              <Select  style={{ width:200}}>
                {
                     zu.length>0 ? zu.map((m,n)=>{
                          return(
                            <Option value={m.id } key={n}>{m.name} </Option>
                          )
                     }) :''
                }
             

              </Select>
            )}
        </Form.Item>
        </Col>

       
        </Row>
        <br />
        <Row gutter={24} >
        
        <Col span={6}>
        <Form.Item label="入职时间">
          {getFieldDecorator('r_time', {
            rules: [{ required: false, message: '请选择!' }],
          })(
            <DatePicker onChange={onChangeDate} />
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="居住地址">
          {getFieldDecorator('address', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>

        <Col span={6}>
        <Form.Item label="生日">
        {getFieldDecorator('birthday', {
            rules: [{ required: false, message: '请选择!' }],
          })(
            <DatePicker onChange={onChangeDate} />
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="电话">
        {getFieldDecorator('urgent', {
            rules: [{ required: false, message: '请输入!' },
                    {validator: this.validFunction}
          ],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
       
       
        </Row>
        <br/>
        <Row>
        <Col span={6}>
        <Form.Item label="登录密码" help='注：密码为8位字母数字组合'>
        {getFieldDecorator('password', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="等级">
            {getFieldDecorator('guide', {
                rules: [{ required: false, message: '请输入渠道总监!' }],
            })(
                <Select
                style={{ width:'200px'}}
                  placeholder="请选择"
                  onChange={this.handleDengChange}
                >
                  <Option value="1">一级</Option>
                  <Option value="2">二级</Option>
                  <Option value="3">三级</Option>
                  <Option value="4">四级</Option>
                  <Option value="5">五级</Option>
                </Select>,
            )}
        </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="上级领导">
            {getFieldDecorator('pid', {
                // rules: [{ required: false, message: '请渠道总监!' }],
            })(
                <Select
                style={{ width:'200px'}}
                  placeholder="请选择"
                  onChange={this.handleSelectChange}
                >
                 { 
                      leader.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      })
                  }
                  
                </Select>,
            )}
        </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="岗位">
            {getFieldDecorator('job', {
                rules: [{ required: false, message: '请输入渠道总监!' }],
            })(
                <Select
                style={{ width:'200px'}}
                  placeholder="请选择"
                  onChange={this.handleSelectChange}
                >
                  
                  { 
                      position.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      })
                  }
                  
                </Select>
            )}
        </Form.Item>
        </Col>
        </Row>
        <br/>
        <br/>
        {/**家庭信息 */}
        <p className="title1 ">家庭信息</p>
        <br/>
        <Row gutter={24} >
        
        <Col span={6}>
        <Form.Item label="户籍地址">
          {getFieldDecorator('huji', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
        </Row>
        <br/>
         {/**家庭成员 */}
         <p className="title1 ">家庭成员</p>
         <br/>
         <Row gutter={24} >
        
        <Col span={6}>
        <Form.Item label="姓名">
          {getFieldDecorator('name1', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="电话">
          {getFieldDecorator('tel1', {
            rules: [{ required: false, message: '请输入!' },  {validator: this.validFunction}],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>

        <Col span={6}>
        <Form.Item label="出生年月">
        {getFieldDecorator('birth1', {
            rules: [{ required: false, message: '请选择!' }],
          })(
            <DatePicker onChange={onChangeDate} />
          )}
        </Form.Item>
        </Col>
       
        </Row>
        <br/>
        <Row gutter={24} >
        
        <Col span={6}>
        <Form.Item label="姓名">
          {getFieldDecorator('name2', {
            rules: [{ required: false, message: '请输入!' }],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>
        <Col span={6}>
        <Form.Item label="电话">
          {getFieldDecorator('tel2', {
            rules: [{ required: false, message: '请输入!' },  {validator: this.validFunction}],
          })(
            <Input  style={{ width:200}}/>
          )}
        </Form.Item>
        </Col>

        <Col span={6}>
        <Form.Item label="出生年月">
        {getFieldDecorator('birth2', {
            rules: [{ required: false, message: '请选择!' }],
          })(
            <DatePicker onChange={onChangeDate} />
          )}
        </Form.Item>
        </Col>
        
        </Row>
        <br/>
        <Row>
              <Form.Item>
                <Button type="primary" htmlType="submit">保存</Button>
              </Form.Item>
          </Row>
    </Form>
          </div>
          )
      }
}
const newaddSell = Form.create()(RegistForm);
  export default newaddSell