import React ,{Component} from 'react';
import {Form, Input, DatePicker,Select  }  from 'antd'
import axios  from 'axios'
const { TextArea } = Input;
const { Option}= Select
const  loginid=localStorage.getItem('loginid');
const user=localStorage.getItem('name');
class cusTiaozheng  extends Component{
      constructor(props){
        super(props)
        this.state={
             area:[],
             ye:[]
        }
      }
      componentDidMount(){
         this.getArea(); 
      }
   
      getArea=()=>{
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
          axios.post('/index.php/user/change',{
             num:loginid,
             name:user,
         })
         .then((res)=>{
           console.log(res);
           if(res.data.code==200){
            console.log('区域');
             var num=res.data.data;
             this.setState({
              area:num
             })
           }
         }).catch((error=>{
            console.log(error);
         }))
      }
    }
   
    selectChangeCustomer=(value)=>{
         console.log(value);
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            axios.post('/index.php/user/get',{
              num:loginid,
              name:user,
              id:value
          })
          .then((res)=>{
           
            if(res.data.code==200){
              console.log(res);
              console.log('业务员');
              var num=res.data.data;
              if(num.length>0){
                this.setState({
                  ye:num
                })
              }
             
            }
          }).catch((error=>{
              console.log(error);
          }))
        }
    
    }

      render(){
        const { area,ye } =this.state;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout={
            labelCol:{
              span:4
            },
            wrapperCol:{
               span:20
              }
        }
        return(
          <div className="news">
               <Form > 
               <Form.Item label="转入区域" {...formItemLayout}>
          {getFieldDecorator('time', {
            rules: [{ required: false, message: '请选择跟进时间!' }],
          })(
            <Select  onChange={this.selectChangeCustomer} >
              {
                  area.length>0?area.map((p,q)=>{
                       return(
                        <Option key={q} value={p.id}>{p.name}</Option>
                       )
                  }):''
              }
            </Select>
           
          )}
        </Form.Item>
        <Form.Item  label="业务员" {...formItemLayout}>
          {getFieldDecorator('t_time', {
            rules: [{ required: false, message: '请选择提醒日期!' }],
          })(
            <Select>
               {
                  ye.length>0?ye.map((p,q)=>{
                       return(
                        <Option key={q} value={p.id}>{p.name}</Option>
                       )
                  }):''
              }
            </Select>
           
          )}
        </Form.Item>
               </Form>
          </div>
          )
      }
}
const cusTiaozhengs=Form.create()(cusTiaozheng)
  export default cusTiaozhengs