import React ,{Component} from 'react';
import { Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Select,
  Cascader,
  Tabs,
  Table,
  Icon,
  LocaleProvider,
  Menu, 
  Dropdown, } from 'antd';
 import {Link} from 'react-router-dom'
 import zhCN from 'antd/lib/locale-provider/zh_CN';
import axios from 'axios' 
const {Option}=Select
const { TabPane } = Tabs;
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
function onChange(value) {
  console.log(value);
}
function callback(key) {
  console.log(key);
}
class staffForm  extends Component{
      constructor(props){
        super(props)
        this.state={
          datanum:[],
          loading:true,
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
          }
        });
      };
      handleSelectChange(){

      }
      tiaoDetail(num){
        this.props.history.push({pathname:'/projectmanage/projectInformation/projectdetail',data:num});
      }
      componentDidMount(){
        var key=this.props.location.data;
        console.log(key);
        //项目列表
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/integral/read',{
          name:user,
          num:loginid,
          id:key
        })
        .then((res)=>{
          if(parseInt(res.status) === 200){
            //  console.log(res);
             var number=res.data.data
             number.map((v,j)=>{
             this.state.datanum.push({
                     key:v.id,
                     yeaction:v.action,
                     time:v.upload_time,
                     jifen:v.integral,
                 })
             var  datanum=this.state.datanum
                 this.setState({
                  datanum:datanum,
                  loading:false
                 })
             })
          }else{
             console.log("请求数据失败")
          }
        }).catch((error)=>{
          console.log(error)
        })
      }
     //区域数据
    

      }
      tiaolink(){
        this.props.history.push('/personmanage/addstaff');
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const columns = [
          {
            title: '业务动作',
            dataIndex: 'yeaction',
          },
          {
            title: '时间',
            dataIndex: 'time',
          },
          {
            title: '积分',
            dataIndex: 'jifen',
          },
         
        ];
     
      

      const paginationProps = {
        showSizeChanger: true,
        showQuickJumper: {goButton:<Button type="primary">确定</Button>},
        showTotal: ((total,count) => {
          console.log(count);
           return `共 ${total} 条`;
        }),
        // showTotal: () => `共${totals}条`,
        // pageSize: this.state.pageSize,
        // current: page.pageNum,
        // total: page.total,
        // onShowSizeChange: (current,pageSize) => this.changePageSize(pageSize,current),
        // onChange: (current) => this.changePage(current),
      };

      const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          disabled: record.name === 'Disabled User', // Column configuration not to be checked
          name: record.name,
        }),
      };
      
       
        
        return(
          <LocaleProvider locale={zhCN}>
          <div className="customerlist">
             <div>
                <Breadcrumb>
                  <Breadcrumb.Item href="/"> 个人信息</Breadcrumb.Item>
                  <Breadcrumb.Item href="">项目积分 </Breadcrumb.Item>
                </Breadcrumb>
             </div>
               <Form onSubmit={this.handleSubmit} className="login-form" layout="inline">
              
                <Form.Item label="业务动作" layout="inline">
                {getFieldDecorator('projectname', {
                    rules: [{ required: false, message: '请输入项目名称' }],
                  })(
                    <Input placeholder="请输入项目名称"/>
                  )}
                  </Form.Item>

                  <Form.Item label="时间" layout="inline">
                {getFieldDecorator('projectname', {
                    rules: [{ required: false, message: '请输入项目名称' }],
                  })(
                    <Input placeholder="请输入项目名称"/>
                  )}
                  
                  </Form.Item>
           
              <Button type="primary" htmlType="submit" style={{width:"88px",height:"32px",marginTop:'3px'}}>
              查询
            </Button>
             </Form>
            <div className="project-table">
                     <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.datanum}   pagination={ paginationProps } loading={this.state.loading} />
                     {/* <p className="yema">共&nbsp;<span>400&nbsp;</span>条&nbsp; 第&nbsp;<i>&nbsp;1/50</i>&nbsp;页</p> */}
                     <Button type="primary"  style={{width:"100px",height:"32px",marginTop:'3px'}} >删除</Button>
                   
            </div>


          </div>
          </LocaleProvider>
          )
      }
}
const jifenstatus = Form.create()(staffForm);
export default jifenstatus