import React, { Component } from 'react';
import { Form, Input, DatePicker,LocaleProvider } from 'antd'
import zhCN from 'antd/lib/locale-provider/zh_CN';
const { TextArea } = Input;

class modeform extends Component {
  constructor(props) {
    super(props)
    this.state = {
        
    }
  }

  componentWillReceiveProps(nextProps) {
      console.log(nextProps)
   
  }
  componentDidMount() {

  }


  render() {
    const { getFieldDecorator } = this.props.form;
    return (
        <LocaleProvider locale={zhCN}>
      <div className="news">
        <Form >
          <Form.Item label="下次电话沟通时间" >
            {getFieldDecorator('time', {
              rules: [{ required: false, message: '请选择跟进时间!' }],
            })(
            //    <DatePicker showTime placeholder="请选择跟进时间" onChange={onChange} onOk={onOk} style={{width:394}} />,
            <Input  placeholder="请选择跟进时间" style={{width:394}} />
            )}
          </Form.Item>
          <Form.Item label="下次沟通内容提醒：" >
            {getFieldDecorator('content', {
              rules: [{ required: false, message: '请输入跟进内容!' }],
            })(
              <TextArea rows={4}  style={{width:394}}/>,
            )}
          </Form.Item>
        </Form>
      </div>
      </LocaleProvider>
    )
  }
}
const modeforms = Form.create()(modeform)
export default modeforms