import React ,{Component} from 'react';
import { 
    Form, 
    Input, 
    Button, 
    Breadcrumb ,
    Row,
    Select
  } from 'antd';
  import '../../../style/projectdongtaiadd.scss'
  import axios from 'axios'

class DongtaiForm extends Component{
      constructor(props){
        super(props)
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key');
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            axios.post('/index.php/guide/save',{
                bid:key,
                value:values,
                name:user,
                num:loginid
            })
            .then((res)=>{
                  if(res.data.code==200){
                    console.log(res);
                    this.props.history.push('/projectmanage/projectInformation/projectlist');
                  }
            }).catch((error)=>{
              console.log(error)
            })
          }
          }
        });
      };
      editDongTai=()=>{
           this.props.history.push('/projectmanage/projectInformation/editdongtai');
      }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const {Option}=Select
        return(
          <div className="projectdongtaiadd">
                 <div className="bread">
                    <Breadcrumb>
                        <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                        <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                        <Breadcrumb.Item href="">项目动态 </Breadcrumb.Item>
                    </Breadcrumb>
                    </div>
                    <Form layout="inline" onSubmit={this.handleSubmit}>
                       <p className="title2">是否加推</p>
                       <Row>
                        <Form.Item>
                        {getFieldDecorator('type', {
                                })(
                            <Select    style={{ width:1000}} placeholder="是">
                                 <Option value="是">是</Option>
                                 <Option value="否">否</Option>
                            </Select>,
                                )}
                        </Form.Item>
                        </Row>
                        <p className="title2">动态内容</p>
                       <Row>
                        <Form.Item >
                        {getFieldDecorator('introduce', {
                                })(
                            <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} placeholder="请输入动态内容"/>,
                                )}
                        </Form.Item>
                        </Row>
                        <Row>
                        <Form.Item>
                        <Button type="primary" onClick={this.editDongTai}>
                                    编辑动态
                      </Button>
                      <Button type="primary" htmlType="submit" className="sub">
                                    上传
                      </Button>
                    </Form.Item>
                    </Row>
                    </Form>
          </div>
          )
      }
}


const ProjectDongTaiAdd = Form.create()(DongtaiForm);
export default ProjectDongTaiAdd
