import React ,{Component} from 'react';
import { 
  Form, 
  Input, 
  Button, 
  Breadcrumb ,
  Col,
  Row,
  Select,

} from 'antd';
import '../../../style/addtuangou.scss'
import axios from 'axios'
const { Option } =Select;
class RegistForm extends Component{
      constructor(props){
        super(props)
        this.state={
              show:true,
              detail:[],
              cone:'',
              ctwo:''
        }
        
      }
      componentDidMount(){
        var key=sessionStorage.getItem('key')
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!='' && user!=""){
          axios.post('/index.php/fen/index',{
            id:key,
            type:1,
            name:user,
            num:loginid
       }).then((res)=>{
              if(res.data.code==200){
               console.log(res.data.data);
               var cone=res.data.data.cone;
               var ctwo=res.data.data.ctwo
               if(cone!=='' && ctwo!==''){
                  this.setState({
                    detail:res.data.data,
                    cone:cone,
                    ctwo:ctwo
                  })
              }else{
                this.setState({
                  detail:res.data.data,
                  cone:'',
                  ctwo:''
                })
               }
             }
       }).catch((error)=>{
             console.log(error)
       })
      }

      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            console.log(key);
            axios.post('/index.php/fen/update',{
              bid:key,
              value:values,
              name:user,
              num:loginid
         }).then((res)=>{
                if(res.data.code==200){
                 console.log('提交分析成功');
                 this.props.history.push('./components/projectManage/projectInformation/projectList');

               }
         }).catch((error)=>{
               console.log(error)
         })
        }

          }
        });
      };
      handleSelectChangeType=(value)=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var key=sessionStorage.getItem('key')
        console.log(value);
        if(value=="1"){

              this.setState({
                    show:true
              })
        }else if(value=="2"){
              this.setState({
                show:false
              })
        }

        if(loginid!='' && user!=""){
          axios.post('/index.php/fen/index',{
            id:key,
            type:value,
            name:user,
            num:loginid
       }).then((res)=>{
              if(res.data.code==200){
               console.log(res.data.data);
               var number=res.data.data;
               var cone=res.data.data.cone;
               var ctwo=res.data.data.ctwo
               if(cone!=='' && ctwo!==''){
                this.setState({
                  detail:res.data.data,
                  cone:cone,
                  ctwo:ctwo
                })
               }else{
                this.setState({
                  cone:'',
                  ctwo:''
                })
               }
              
             }
       }).catch((error)=>{
             console.log(error)
       })
      }



      }
     
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { show ,detail,cone,ctwo } =this.state;
        return(
    <div className="addtuangou">
            <div className="bread">
        <Breadcrumb>
            <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
            <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
            <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
            <Breadcrumb.Item href="">项目分析 </Breadcrumb.Item>
        </Breadcrumb>
        </div>
        <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">分析类别：</p>
            <Form.Item >
            {getFieldDecorator('type', {
              //  initialValue:'投资分析'
                })(
            <Select style={{ width:1000}}  onChange={this.handleSelectChangeType} placeholder="投资分析">
                <Option value="1">投资分析</Option>
                <Option value="2">宜居分析</Option>
            </Select>
                )}
            </Form.Item >
        </Row>
        <br/>
        {show==true?
        <div>
        <Row>
            <p  className="title2">分析内容1：</p>
            <Form.Item >
            {getFieldDecorator('cone', {
                 initialValue:cone 
                })(
                  <TextArea rows={4} placeholder='周边土地价格'  autosize={{ minRows: 4, maxRows: 6 }} />,
            )}
            </Form.Item >
        </Row> 
        <br/>
       <Row> 
            <p  className="title2">分析内容2：</p>
            <Form.Item >
            {getFieldDecorator('ctwo', {
               initialValue:ctwo
                })(
                  <TextArea rows={4} placeholder='周边二手房或租赁价格'  autosize={{ minRows: 4, maxRows: 6 }} />,
            )}
            </Form.Item >
        </Row>
        <br/> 
        </div> :
         <div>
        <Row>
            <p  className="title2">分析内容1：</p>
            <Form.Item >
            {getFieldDecorator('cone', {
                initialValue:cone
                })(
                  <TextArea rows={4} placeholder='周边交通配套（地铁......）'  autosize={{ minRows: 4, maxRows: 6 }} />,
            )}
            </Form.Item >
        </Row>
        <br/>
        <Row>
            <p  className="title2">分析内容2：</p>
            <Form.Item >
            {getFieldDecorator('ctwo', {
                initialValue:ctwo
                })(
                  <TextArea rows={4} placeholder='周边商业配套或公园配套'  autosize={{ minRows: 4, maxRows: 6 }} />,
            )}
            </Form.Item >
        </Row>
        <br/>
        </div>}

        <Form.Item>
          <Button type="primary" htmlType="submit" className="sub">
          提交
          </Button>
        </Form.Item>
          </Form>
   </div>
          )
      }
}
const Addtuangou = Form.create()(RegistForm);
export default Addtuangou