import React ,{Component} from 'react';
import {Form, Input, DatePicker ,Row,Col , Select}  from 'antd'
const {Option } =Select
const { TextArea } = Input;
function onChange(value, dateString) {
  // console.log('Selected Time: ', value);
  // console.log('Formatted Selected Time: ', dateString);
}

function onOk(value) {
  // console.log('onOk: ', value);
}

class modeform  extends Component{
      constructor(props){
        super(props)
        this.state={
            code:'',
            zongjia:'',
            jianmian:'',
            loujia:'',
            unit:'',
            time:'',
            bian:'',
            address:'',
        }
      }
   componentWillReceiveProps(nextProps){
         console.log(nextProps.xiudata);
        this.setState({
            code:nextProps.xiudata.code,
            zongjia:nextProps.xiudata.zongjia,
            jianmian:nextProps.xiudata.jianmian,
            loujia:nextProps.xiudata.loujia,
            unit:nextProps.xiudata.unit,
            time:nextProps.xiudata.time,
            bian:nextProps.xiudata.bian,
            address:nextProps.xiudata.address,  
        })
   }
      render(){
        const { getFieldDecorator } = this.props.form;
        const {code,
        zongjia,
        jianmian,
        loujia,
        unit,
        time,
        bian,
        address}=this.state
        return( 
          <div className="news">
               <Form layout="inline"> 
           <Row  gutter={24}>
             <Col span={12}>
                    <Form.Item label="土拍编号" >
                {getFieldDecorator('code', {
                    rules: [{ required: false, message: '请输入土拍编号!' }],
                    initialValue:code
                })(
                    <Input style={{width:180}} placeholder="请输入土拍编号"/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="成交总额" >
                {getFieldDecorator('zongjia', {
                    rules: [{ required: false, message: '请输入成交总额!' }],
                    initialValue:zongjia
                })(
                    <Input placeholder="请输入成交总额" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="总建筑面积" >
                {getFieldDecorator('jianmian', {
                     initialValue:jianmian
                })(
                   <Input placeholder="请输入总建筑面积" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="楼面价" >
                {getFieldDecorator('loujia', {
                     initialValue:loujia
                })(
                    <Input placeholder="请输入楼面价" style={{width:180}}/>
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="竞拍单位" >
                {getFieldDecorator('unit', {
                     initialValue:unit
                
                })(
                    <Input placeholder="请输入竞拍单位" style={{width:180}}/>
  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="成交日期" >
                {getFieldDecorator('time', {
                     initialValue:time
                })(
                    // <DatePicker showTime placeholder="请选择成交日期" onChange={onChange} onOk={onOk} style={{width:170}} />
                    <Input  placeholder="请选择成交日期" style={{width:170}} />
                )}
                </Form.Item>
            </Col>   
        </Row>

        <Row gutter={24}>
             <Col span={12}>
                    <Form.Item label="土地变更" >
                {getFieldDecorator('bian', {
                     initialValue:bian
                })(
                    <Input  style={{width:180}} placeholder="土地变更"/>
                        
                  
                )}
                </Form.Item>
            </Col>
            <Col span={12}>
                <Form.Item  label="土地位置" >
                {getFieldDecorator('address', {
                    rules: [{ required: false, message: '请输入土地位置!' }],
                    initialValue:address
                })(
                    <Input placeholder="登记人" style={{width:180}} />
                )}
                </Form.Item>
            </Col>   
        </Row>
               </Form>
          </div>
          )
      }
}
const modeforms=Form.create()(modeform)
  export default modeforms