import React from 'react';
import '../style/editTable.scss';
import axios from 'axios'
import { Table, Input, InputNumber, Popconfirm, Form } from 'antd';

const data = [];
for (let i = 0; i < 100; i++) {
  data.push({
    key: i.toString(),
    tag:'ABC',
    name:'林先生',
    age: 32,
    project:'众安嘉润公馆',
    origin:'线上推广1',
    time:'19-06-08 08:26',
    gtime:'19-06-08 08:26',
    date:'19-06-08 08:26',
    registrant:'林方',
    client:'王铁',

  });
}
const EditableContext = React.createContext();

class EditableCell extends React.Component {
  getInput = () => {
    if (this.props.inputType === 'number') {
      return <InputNumber />;
    }
    return <Input />;
  };

  renderCell = ({ getFieldDecorator }) => {
    const {
      editing,
      dataIndex,
      title,
      inputType,
      record,
      index,
      children,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item style={{ margin: 0 }}>
            {getFieldDecorator(dataIndex, {
              rules: [
                {
                  required: true,
                  message: `请填写 ${title}!`,
                },
              ],
              initialValue: record[dataIndex],
            })(this.getInput())}
          </Form.Item>
        ) : (
          children
        )}
      </td>
    );
  };

  render() {
    return <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>;
  }
}

class EditableTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      data,
      editingKey: '',
      tableData:[],
      newtabledata:[]
     };

    this.columns = [
      {
        title: '标签',
        dataIndex: 'tag',
        editable: true,
      },
      {
        title: '客户姓名',
        dataIndex: 'name',
        editable: true,
      },
      {
        title: '进客项目',
        dataIndex: 'project',
        editable: true,
      },
      {
        title: '进客来源',
        dataIndex: 'origin',
        editable: true,
      },
      {
        title: '登记时间',
        dataIndex: 'time',
        editable: true,
      },
      {
        title: '最近跟进时间',
        dataIndex: 'gtime',
        editable: true,
      },
      {
        title: '提醒日期',
        dataIndex: 'date',
        editable: true,
      },
      {
        title: '登记人',
        dataIndex: 'registrant',
        editable: true,
      },
      {
        title: '委托人',
        dataIndex: 'client',
        editable: true,
      },
      {
        title: '操作',
        dataIndex: 'operation',
        render: (text, record) => {
          const { editingKey } = this.state;
          const editable = this.isEditing(record);
          return editable ? (
            <span>
              <EditableContext.Consumer>
                {form => (
                  <a
                    href="javascript:;"
                    onClick={() => this.save(form, record.key)}
                    style={{ marginRight: 8 }}
                  >
                    保存
                  </a>
                )}
              </EditableContext.Consumer>
              <Popconfirm title="确认取消吗?" onConfirm={() => this.cancel(record.key)} cancelText="取消" okText="确认">
                <a>取消</a>
              </Popconfirm>
            </span>
          ) : (
            <a disabled={editingKey !== ''} onClick={() => this.edit(record.key)}>
              编辑
            </a>
          );
        },
      },
    ];
  }
componentDidMount(){
    // axios.get('https://www.easy-mock.com/mock/5cf520e492cde15aa38040f5/example/table',{})
    // .then((res)=>{
    //     console.log(res.data.data.data)
    //     this.setState({
    //       tableData:res.data.data.data
    //     })
    //  if(this.state.tableData.length>0) {
    //       this.state.tableData.map((item,index)=>{
    //         this.state.data.push({
    //           key:index,
    //           tag:item.tag,
    //           name:item.name,
    //           project:item.project,
    //           origin:item.origin,
    //           time:item.rtime,
    //           gtime:item.gtime,
    //           date:item.txTime,
    //           registrant:item.rname,
    //           client:item.wname,
    //         });
           
    //       })
    //  } 
    // }).then((error)=>{
    //   console.log(error)
    // })
}



  isEditing = record => record.key === this.state.editingKey;

  cancel = () => {
    this.setState({ editingKey: '' });
  };

  save(form, key) {
    form.validateFields((error, row) => {
      if (error) {
        return;
      }
      const newData = [...this.state.data];
      const index = newData.findIndex(item => key === item.key);
      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, {
          ...item,
          ...row,
        });
        this.setState({ data: newData, editingKey: '' });
      } else {
        newData.push(row);
        this.setState({ data: newData, editingKey: '' });
      }
    });
  }

  edit(key) {
    this.setState({ editingKey: key });
  }

  render() {
    const components = {
      body: {
        cell: EditableCell,
      },
    };

    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          inputType: col.dataIndex === 'age' ? 'number' : 'text',
          dataIndex: col.dataIndex,
          title: col.title,
          editing: this.isEditing(record),
        }),
      };
    });

    return (
      <EditableContext.Provider value={this.props.form}>
        <Table
          components={components}
          bordered={false}
          dataSource={this.state.data}
          columns={columns}
          rowClassName="editable-row"
          pagination={{
            onChange: this.cancel,
            pageSize:8
          }}
          style={{ width:'1040px',height:"360px"}}
        />
          <p>共&nbsp;&nbsp;400&nbsp;&nbsp;条&nbsp;&nbsp;第&nbsp;3&nbsp;页</p>
      </EditableContext.Provider>
    );
  }
}

const EditableFormTable = Form.create()(EditableTable);
export default EditableFormTable


          