import React,{ Component} from 'react'
import zhCN from 'antd/lib/locale-provider/zh_CN';
import { Breadcrumb,LocaleProvider,}  from 'antd'
import  '../../../style/statistics/kedetail.scss'
import axios from 'axios'
import $ from 'jquery'
import { Link  } from 'react-router-dom'
var loginid=localStorage.getItem('loginid');
var user=localStorage.getItem('name');
class kedetail extends Component{
       constructor(props){
              super(props)
              this.state={
                     data:[],
              }
       }
       componentDidMount(){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
           var num=1;
        if(loginid!='' && user!=""){
            axios.post('/index.php/user/xiang',{
              name:user,
              num:loginid,
              type:num
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  this.setState({
                         data:res.data.data
                  })
               
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
  
          } 
       }
       
       selectDate=(e)=>{
        var num=e.target.getAttribute('data-key');
        localStorage.setItem('dtype',num);
         console.log(num);  
         this.getKeLiang();
         $(e.target).addClass("current").siblings().removeClass("current")
       }
       getKeLiang=()=>{
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
           var num=localStorage.getItem('dtype');
           console.log(num); 
        if(loginid!='' && user!=""){
            axios.post('/index.php/user/xiang',{
              name:user,
              num:loginid,
              type:num
            })
            .then((res)=>{
              if(parseInt(res.status) === 200){
                  console.log(res.data.data);
                  this.setState({
                         data:res.data.data
                  })
               
              }else{
                 console.log("请求数据失败")
              }
            }).catch((error)=>{
              console.log(error)
            })
  
          } 
       }

       render(){
           const { data }= this.state;
           return(
 <LocaleProvider locale={zhCN}>
              <div className="kedetail">
        <Breadcrumb>
              <Breadcrumb.Item  ><Link to="/statistics/kedetail"> 统计分析</Link></Breadcrumb.Item>
              <Breadcrumb.Item ><Link to="/statistics/kedetail">客户量详情</Link> </Breadcrumb.Item>
          </Breadcrumb>
          <div className="option">
               
                <ul className="time" style={{  cursor:'pointer'}}>
                        <li onClick={this.selectDate.bind(this)} data-key={'1'} >日</li>
                        <li onClick={this.selectDate.bind(this)} data-key={'2'}>周</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'3'}>月</li>
                        <li onClick={this.selectDate.bind(this)}  data-key={'4'}>年度</li>
                </ul>
              
        </div>
        <div className="tablelist">
                    <table border="1" >
                        <thead>
                            <tr>
                                <th></th>
                               <th>项目进客</th>
                               <th>带看</th>
                               <th>占比</th>
                               <th>上期</th>
                               <th>环比</th>
                            </tr>
                        </thead>
                          <tbody>
                              {
                                 data.length>0 ? data.map((m,n)=>{
                                         return(
                                            <tr>
                                            <td>{m[0]}</td>
                                            <td>{m[1]}</td>
                                            <td>{m[2]}</td>
                                            <td>{m[3]}%</td>
                                            <td>{m[4]}%</td>
                                            <td>{m[5]}%</td>
                                        </tr>
                                         )
                                 })  :'' 
                              }
                              
                          </tbody>
                    </table>
        </div>
              </div>
 </LocaleProvider>
            )
       }
}
export default kedetail