  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      var id=localStorage.getItem('customerid');
      if (!err) {
        console.log('输入的结果为 ', values);
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/recording/save',{
            id:id,
           value:values,
           element:this.state.element,
           protocol:this.state.protocol,
           confirm:this.state.confirm,
           site:this.state.site,
           from:'电脑端',
           name:user,
           num:loginid
        })
        .then((res)=>{
           console.log(res)
           if(res.data.code==200){
            console.log('提交成功 ')
            this.props.history.push('/customermanage/customerlist');
           }
        }).catch((error)=>{
            console.log(error)
        })
      }

      }
    });
  };
//成交项目列表
